import React from "react";
import {yUpperScale,yDownerScale} from './MapViz'
import * as d3 from 'd3';
import {GrafanaTheme2} from "@grafana/data";
import {stylesFactory} from "@grafana/ui";
import {css} from "@emotion/css";

interface Props extends React.SVGProps<SVGSVGElement>{
    drawThreshold:number,
    min:number,
    max:number,
    width:number,
    theme: GrafanaTheme2
}
interface drawdata {values:{x:number,y:number}[],render:Function,class:string}
interface States {
    data: drawdata[]
}
export class StreamLegend extends React.Component<Props, States> {

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {drawThreshold,min,max,width,theme,...other} = this.props;
        let upScale= yUpperScale;
        let downScale= yDownerScale;
        let streamPos =+0+0+upScale.range()[1];
        let streamxOffset = 80;
        let streamxScale = d3.scaleLinear().range([streamxOffset,width-30]);
        const scaleService = d3.scaleLinear().domain([min,max]);
        let area_up = d3.area<{x:number,y:number[]}>()
            .curve(d3.curveCatmullRom)
            .x(function (d) {
                return streamxScale(d.x) as number;
            })
            .y0(function (d) {
                return -(upScale(d.y[0]) as number);
            })
            .y1(function (d) {
                return -(upScale(d.y[1]) as number);
            });
        let area_down = d3.area<{x:number,y:number[]}>()
            .curve(d3.curveCatmullRom)
            .x(function (d) {
                return streamxScale(d.x) as number;
            })
            .y0(function (d) {
                return -(downScale(d.y[0]) as number);
            })
            .y1(function (d) {
                return -(downScale(d.y[1]) as number);
            });
        let threshold = this.props.drawThreshold;
        const data = [{values:getUpdtream(d3.range(0,21).map(d=>d/50)),render:area_up,class:'upStream'},
            {values:getDowndtream(d3.range(20,41).map(d=>d/50)),render:area_down,class:'downStream'},
            {values:getUpdtream(d3.range(40,46).map(d=>d/50)),render:area_up,class:'emptyStream'},
            {values:getDowndtream(d3.range(45,51).map(d=>d/50)),render:area_down,class:'emptyStream'}
        ]
        function getUpdtream(range:number[]) {
            let upStream = range.map(d => ({x: d , y: [0, Math.random() * (1 - threshold)]}));
            upStream.push({x: range[range.length-1], y: [0, 0]});
            upStream[0].y[1] = 1 - threshold;
            upStream[1].y[1] = 1 - threshold;
            return upStream;
        }
        function getDowndtream(range:number[]) {
            let downStream = range.map(d=>({x:d,y:[Math.random()*(-threshold),0]}));
            downStream.push({x:range[range.length-1],y:[-threshold,0]});
            downStream[downStream.length-2].y[0] =-threshold;
            return downStream;
        }
        const styles = getStyles(theme);
        return <svg width={width} height={(downScale(1)??0)+45} className={styles.svg} {...other}>
            <defs>
                <marker id="arrow" viewBox="0 -5 10 10" refX="5" refY="0" markerWidth="4" markerHeight="4"
                        orient="auto">
                    <path d="M0,-5L10,0L0,5" className="arrowHead" fill={theme.colors.text.primary}></path>
                </marker>
            </defs>
            {[1-threshold,-threshold].map(d=><g className={'streamMarker streamlegendItem'} transform={`translate(0,${streamPos-((d>0?upScale:downScale)(d) as number)})`}>
                <line className={'threshold streamlegendItem'} strokeDasharray={'2 1'} stroke={this.props.theme.colors.text.primary} strokeWidth={0.5} x1={streamxScale(0)} x2={streamxScale(1)}/>
                <text className={'streamlegendItem'} fill={this.props.theme.colors.text.primary} x={streamxScale(0.5)} dy={d<0?'1rem':'0'} textAnchor={'middle'}>{+d3.format('.2f')(scaleService.invert(d+threshold))}</text>
            </g>)}
            {data.map(d=><path className={'stream streamlegendItem '+d.class} transform={`translate(0,${streamPos})`} d={''+d.render(d.values)}/>)}
            <line className={'streamMid streamlegendItem'} transform={`translate(${streamxOffset-20},${streamPos})`} x2={20} stroke={this.props.theme.colors.text.primary} markerEnd={'url(#arrowhead)'}/>
            <line className={'nojob streamlegendItem'} transform={`translate(0,${streamPos})`} x1={streamxScale(40/50)} x2={streamxScale(40/50)}
                  y1={downScale(0)} y2={(downScale(1)??0)+12} strokeDasharray={'2 1'} stroke={this.props.theme.colors.text.primary}/>
            <line className={'nojob streamlegendItem'} transform={`translate(0,${streamPos})`} x1={streamxScale(1)} x2={streamxScale(1)}
                  y1={downScale(0)} y2={(downScale(1)??0)+12} strokeDasharray={'2 1'} stroke={this.props.theme.colors.text.primary}/>
            <line className={'nojob streamlegendItem'} transform={`translate(0,${streamPos+ (downScale(1)??0) + 5})`} x1={streamxScale(40/50)} x2={streamxScale(1)}
                  markerEnd={'url(#arrowhead)'} markerStart={'url(#arrowhead)'} stroke={this.props.theme.colors.text.primary}/>
            <text className={'nojob streamlegendItem'} transform={`translate(0,${streamPos+ (downScale(1)??0) + 5})`} x={streamxScale(45/50)} dy={14}
                  textAnchor={'middle'} >no running jobs</text>
        </svg>;
    }
}
const getStyles = stylesFactory((theme:GrafanaTheme2) => {
    const upStreamColor = "rgb(252,141,89)";
    const downStreamColor = "#4682b4";
    const emptyStreamColor = "rgb(0,0,0)";//"rgba(112,112,112,0.5)";

    return {svg:css`
            & .fade 
            {opacity: 0.2;}
            & text
            {fill: ${theme.colors.text.primary}}
            & .upStream
            {fill:${upStreamColor}}
            & .downStream
            {fill:${downStreamColor}}
            & .emptyStream
            {fill:${emptyStreamColor}}
           `}
})
