import React from 'react';
import {DataFrame, PanelProps} from "@grafana/data";
import {SimpleOptions} from 'types';
import {css} from 'emotion';
// import {stylesFactory} from '@grafana/ui';
import MapViz, { JobOBject, schemeData, CompOBject, UserOBject, DataArr, SummaryArr, TSNEDATA} from './MapViz'
// import{Timeline} from './Timeline'
import * as d3 from 'd3';
import {ScaleLinear} from "d3-scale";
import * as _ from "lodash"
import { stylesFactory} from "@grafana/ui";
import  { ThemeContext } from '@grafana/ui';

import {Drawer} from "./drawer";
import { StreamLegend } from 'StreamLegend';
// import { StreamLegend } from 'StreamLegend';

interface DataObject extends Record<string,Date|number|string|undefined|null> {
    order: number
};
type DataByTime = Record<string,Record<string,DataObject>>;

interface Props extends PanelProps<SimpleOptions> {
}

interface DataArrOp extends Array<any> {
    name?: string,
    timestep?: number
}
interface States {
    datas: Record<string,any>[],
    dataByTime: DataByTime,
    data:DataObject[],
    // jobs: JobOBject[],
    dimensions: Dimension[],
    selectedService?: number,
    timeStamp: Date[],
    scalezoom?:1,
    margin?: {top:number,bottom:number,left:number,right:number},
    scheme: schemeData,
    drawThreshold: number,
}

interface Dimension {
    range: [number,number] | [undefined, undefined],
    scale: ScaleLinear<any, any>,
    order: number,
    key?: string,
    text: string,
    active: number[],
    tickFormat?: Function,
    angle?:number
}
export class MainViz extends React.Component<Props, States>{
    svgRef = React.createRef<SVGSVGElement>();
    widthView(){return this.props.width*(this.state.scalezoom??1)}
    heightView(){return this.props.height*(this.state.scalezoom??1)}
    widthG(){return this.widthView()-(this.state.margin??{top:0,bottom:0,left:0,right:0}).left-(this.state.margin??{top:0,bottom:0,left:0,right:0}).right}
    heightG(){return this.heightView()-(this.state.margin??{top:0,bottom:0,left:0,right:0}).top-(this.state.margin??{top:0,bottom:0,left:0,right:0}).bottom}
    centerX(){return (this.state.margin??{top:0,bottom:0,left:0,right:0}).left+this.widthG()/2;}
    centerY(){return (this.state.margin??{top:0,bottom:0,left:0,right:0}).top+this.heightG()/2;}
    r(){return (d3.min([this.props.width, this.props.height])??0) / 2-20}
    public static defaultProps = {
        width:100,
        height:100,
        margin :{top:0,bottom:0,left:0,right:0},
        scalezoom: 1,
        // nodePerRing: 50,
        // nodeSize:5
    }
    state:States = {
            data: [],
            datas: [],
            dataByTime: {},
            dimensions: [],
            selectedService: 0,
            timeStamp:[],
            scheme: {
                    computers: {},
                    emptyMap: {},
                    jobArr: {},
                    jobs: {},
                    minMaxData: {},
                    timespan: [],
                    tsnedata: {},
                    userArr: {},
                    users: {},
                },
        drawThreshold:650 / 700
        }
    constructor(props:Props) {
        super(props);
    }
    componentDidMount(){
        const opt:Partial<States> = this.handleData();
        if (opt.dimensions)
            opt.selectedService = 0;
        this.setState(opt as NonNullable<States>);
    }
    componentDidUpdate(prevProps: Readonly<Props>, prevState: Readonly<States>, snapshot?: any): void {
        if (this.props.data!==prevProps.data){
            const opt:Partial<States> = this.handleData();
            if (opt.dimensions)
                opt.selectedService = 0;
            this.setState(opt as NonNullable<States>);
        }
    }
    sorByService(data: DataObject[],service: keyof DataObject){
        data.sort((a:DataObject,b:DataObject)=>((a[service] as number) - (b[service] as number)))
            .forEach((d,i)=>d.order=i);
    }
    handleData(){
        const {data} = this.props;
        const {userEncoded} = this.props.options;
        const dataByTime: DataByTime = {};
        const datas = new Array();
        let nodeLists = new Map();
        const dimensions:Dimension[] = [];
        let dimensionObject: Record<string,Dimension> = {};
        let timeObject: Record<string,Date> = {};
        const scheme: schemeData = {
            computers: {},
            emptyMap: {},
            jobArr: {},
            jobs: {},
            minMaxData: {},
            timespan: [],
            tsnedata: {},
            userArr: {},
            users: {},
        }
        function getDim(range: [number,number], order: number, dim: string): Dimension {
            const scale = d3.scaleLinear().domain(range);

            const dimControl: Dimension = {
                range,
                scale,
                order,
                text: dim,
                active: new Array<number>(),
                tickFormat: undefined
            };
            return dimControl;
        }

        function handleDataWithname(serie: DataFrame) {
            const dim = serie.refId ?? '';
            const times = serie.fields.find(e => e.name === 'time');
            const values = serie.fields.find(e => e.name === 'value');
            const nodeids = serie.fields.find(e => e.name === 'name');
            const dimTarget:( Dimension | undefined ) = dimensionObject[dim];
            let range: [number, number] = dimTarget?[dimTarget.range[0]??Infinity,dimTarget.range[1]??-Infinity]:[Infinity, -Infinity];
            if (nodeids && nodeids.values && times && times.values && values && values.values) {
                const nodeid = nodeids.values.toArray();
                nodeid.forEach((id, i) => {
                    if (!dataByTime.hasOwnProperty(id)) {
                        dataByTime[id] = {};
                    }
                    const val = values.values.get(i);
                    if (!dataByTime[id][times.values.get(i)]) {
                        const item = {time: times.values.get(i),order:0, id};
                        datas.push(item);
                        nodeLists.set(id, true);
                        dataByTime[id][times.values.get(i)] = item;
                    }
                    dataByTime[id][times.values.get(i)][dim] = val;
                    if (val < range[0])
                        range[0] = val;
                    if (val > range[1])
                        range[1] = val;
                })
            }
            if (!dimTarget) // new Dimension
            {
                dimensionObject[dim] = getDim(range, dimensions.length, dim);
                dimensions.push(dimensionObject[dim]);
            }else{
                dimTarget.range = range;
                dimTarget.scale.domain(range);
            }
        }
        function handleDataWithnames(serie: DataFrame) {
            const times = serie.fields.find(e => e.name === 'time');
            if (times) {
                serie.fields.filter(e => e.type === 'number').forEach(d => {
                    if(d.labels) {
                        let dim = d.name;
                        let id = '';
                        if (dim==='value'){
                            let str = Object.values(d.labels)[0].split('|');
                            id = str[0].trim();
                            dim = str[1].trim();
                        }else{
                            id = Object.values(d.labels)[0].replace('|','').trim();
                        }
                        if (!dataByTime[id]) {
                            dataByTime[id]= {};
                        }
                        const dimTarget: (Dimension | undefined) = dimensionObject[dim];
                        let range: [number, number] = dimTarget ? [dimTarget.range[0] ?? Infinity, dimTarget.range[1] ?? -Infinity] : [Infinity, -Infinity];
                        d.values.toArray().forEach((val, i) => {
                            if (!dataByTime[id][times.values.get(i)]) {
                                const item = {time: times.values.get(i),order:0, id};
                                datas.push(item);
                                nodeLists.set(id, true);
                                dataByTime[id][times.values.get(i)] = item;
                            }
                            dataByTime[id][times.values.get(i)][dim] = val;
                            if (val!==null) {
                                if (val < range[0])
                                    range[0] = val;
                                if (val > range[1])
                                    range[1] = val;
                            }
                        });
                        if (!dimTarget) // new Dimension
                        {
                            dimensionObject[dim] = getDim(range, dimensions.length, dim);
                            dimensions.push(dimensionObject[dim]);
                        } else {
                            dimTarget.range = range;
                            dimTarget.scale.domain(range);
                        }
                    }
                })
            }
        }

        function handleJobData(serie:DataFrame,userEncoded:boolean):Record<string, JobOBject> {
            const job_ids = serie.fields.find(e => e.name === 'job_id');
            const job_names = serie.fields.find(e => e.name === 'name');
            const node_lists = serie.fields.find(e => e.name === 'nodes');
            const user_names = serie.fields.find(e => e.name === 'user_name');
            const start_times = serie.fields.find(e => e.name === 'start_time');
            const end_times = serie.fields.find(e => e.name === 'end_time');
            // console.log(serie)
            const jobs:Record<string, JobOBject>={};
            const userEncode:Record<string, string> = {};
            if(user_names){
                if (userEncoded){
                    let userCodecount = 1;
                    user_names.values.toArray().forEach((name,i)=>{
                      if(!userEncode[name]){
                          userEncode[name] = 'user'+userCodecount;
                          userCodecount++;
                      }
                    })
                }else{
                    user_names.values.toArray().forEach((name,i)=>{
                        userEncode[name] = name;
                    })
                }
            }
            if(job_ids&&job_names&&node_lists&&user_names&&start_times&&end_times){
                job_ids.values.toArray().map((job_id,i)=>{
                    const user_name = userEncode[user_names.values.get(i)];
                    const job_name = job_names.values.get(i);
                    const start_time = start_times.values.get(i)*1000;
                    const end_time = end_times.values.get(i)*1000;
                    const nodestring = node_lists.values.get(i).replace(/\{|\}/g,'');
                    const node_list = nodestring===''?[]:nodestring.split(',');
                    const node_list_obj: Record<string, number> = {};
                    node_list.forEach((n:string)=>node_list_obj[n]=1);
                    if(node_list.length)
                        jobs[job_id] = {
                            job_id,
                            user_name,
                            job_name,
                            node_list,
                            node_list_obj,
                            _node_list:node_list.slice(),
                            _node_list_obj:{...node_list_obj},
                            summary: [],
                            start_time,
                            finish_time:end_time
                        }
                });
            }
            return jobs;
        }

        data.series.forEach((serie) => {
            if(serie.fields.length) {
                const time = serie.fields.find(e => e.name === 'time');
                if (time) {
                    if (!(serie.fields.find(e => e.name === 'jobs') && serie.fields.find(e => e.name === 'cpus'))) {
                        time.values.toArray()
                            .forEach(t => {
                                timeObject[t] = t;
                            });
                        if (serie.fields.find(e => e.name === 'name'))
                            handleDataWithname(serie);
                        else
                            handleDataWithnames(serie)
                    }
                } else {
                    scheme.jobs = handleJobData(serie, userEncoded);
                }
            }
        });

        const timeStamp = Object.values<Date>(timeObject).sort((a,b)=>+a - (+b))??[];
        dimensions.sort((a, b) => a.order - b.order).forEach((d,i)=>{
            d.order = i;
            d.angle = (i/dimensions.length)*2*Math.PI;
        });
        const jobs = scheme.jobs;
        //add none value node
        Object.keys(jobs).forEach(k=> {
            const d = jobs[k];
            d._node_list = d.node_list.slice();
            d._node_list.forEach(id => {
                if (!dataByTime[id]) {
                    dataByTime[id] = {};
                    timeStamp.forEach(_t=>{
                        const t = ''+_t;
                        const val = undefined;
                        const item = {time: t,order:0, id};
                        datas.push(item);
                        nodeLists.set(id, true);
                        dataByTime[id][t] = item;
                        dimensions.forEach(dim=>{
                            dataByTime[id][t][dim.text] = val;
                        })
                    })
                }
            });
        });
        const {computers,tsnedata} = this.handleSmalldata(dimensions,dataByTime,timeStamp);
        scheme.computers=computers;
        // remove not exist comp
        Object.keys(jobs).forEach(k=> {
            const d = jobs[k];
            d.node_list = [];
            d._node_list.forEach(comp => {
                if (computers[comp])
                    d.node_list.push(comp);
                else
                    delete d.node_list_obj[comp]
            });
            if(d.node_list.length===0)
                delete jobs[k]
        });

        const {users} = this.handleCUJ(scheme,timeStamp);
        const result = this.handleDataComputeByUser(computers,jobs,timeStamp);
        const  jobCompTimeline = result.data;
        const noJobMap = result.noJobMap;
        const {jobTimeline, jobarrdata,userarrdata,minMaxDataCompJob,minMaxDataUser} = this.handleDataComputeByJob({tsnedata,computers,jobs,users,timespan:timeStamp});

        Object.values(jobs).forEach(d=>{
            d.summary = dimensions.map((s,si)=>{
                const val:SummaryArr = {mean: jobarrdata[d.job_id]?d3.mean<DataArr>(jobarrdata[d.job_id],(d:DataArr)=>d?(d[si] as number):undefined):null,
                min : minMaxDataCompJob[d.job_id]?d3.min(minMaxDataCompJob[d.job_id],d=>d?d[0][si]:undefined):null,
                max : minMaxDataCompJob[d.job_id]?d3.max(minMaxDataCompJob[d.job_id],d=>d?d[1][si]:undefined):null,
                num : jobarrdata[d.job_id].filter(d=>((d?d[si]:undefined) !==undefined)).length}
                return val
            })
        });
        Object.keys(users).forEach(k=>{
            const d = users[k];
            d.total_nodes = d.node.length;
            d.summary = dimensions.map((s,si)=>{
                const val = {mean: userarrdata[k]?d3.mean(userarrdata[k],d=>d?d[si]:undefined):null,
                min : minMaxDataUser[k]?d3.min(minMaxDataUser[k],d=>d?d[0][si]:undefined):null,
                max : minMaxDataUser[k]?d3.max(minMaxDataUser[k],d=>d?d[1][si]:undefined):null,
                num : userarrdata[k].filter(d=>((d?d[si]:undefined) !==undefined)).length};
                return val
            })
        });

        Object.keys(computers).forEach(k=>{
            const d = computers[k];
            d.summary = dimensions.map((s,si)=>{
                const val = {mean: tsnedata[k]?d3.mean(tsnedata[k],d=>d?d[si]:undefined):null,
                min : tsnedata[k]?d3.min(tsnedata[k],d=>d?d[si]:undefined):null,
                max : tsnedata[k]?d3.max(tsnedata[k],d=>d?d[si]:undefined):null,
                num : tsnedata[k]?tsnedata[k].filter(d=>((d?d[si]:undefined) !==undefined)).length:0};
                return val
            })
        });

        scheme.users=users;
        scheme.timespan=timeStamp;
        scheme.tsnedata=tsnedata;
        scheme.emptyMap=noJobMap;
        scheme.jobArr=jobarrdata;
        scheme.userArr=userarrdata;

        console.log(scheme)
        return ({dimensions,dataByTime,timeStamp,
            datas,jobCompTimeline,jobTimeline,
        scheme})
    }
    handleSmalldata(dimensions:Dimension[],dataByTime:DataByTime,timeStamp:Date[]){
        const tsnedata:TSNEDATA = {};
        const computers:Record<string,CompOBject> ={};
        Object.keys(dataByTime).forEach(comp=>{
            const compData = dataByTime[comp];
            tsnedata[comp] = [];
            computers[comp]={
                job_id: [],
                key: comp,
                user: [],
                drawData:[]
            }
            dimensions.forEach(dim=> computers[comp][dim.text]=[]);
            timeStamp.forEach((t,ti)=>{
                const current = compData[''+t];
                const d: DataArrOp = dimensions.map(dim=>{
                    if(current && (current[dim.text]!==null)){
                        computers[comp][dim.text][ti] = current[dim.text];
                        return dim.scale(current[dim.text] as number);
                    }
                    return null
                });
                d.name = comp;
                d.timestep = ti;
                tsnedata[comp][ti] = d as DataArr;
            });

        })
        return {computers,tsnedata}
    }
    handleDataComputeByJob(input:Partial<schemeData>){
        const tsnedata = input.tsnedata??{};
        const computers:Record<string, CompOBject>=input.computers??{};
        const _jobs:Record<string, JobOBject>=input.jobs??{};
        const _users:Record<string, UserOBject> = input.users??{};
        const jobs:Record<string, JobOBject> = {};
        const users:Record<string, UserOBject> = {};
        const jobarrdata:Record<string, DataArr[]> = {};
        const _jobarrdata:Record<string, DataArr[][]> = {};
        const userarrdata:Record<string, DataArr[]> = {};
        const _userarrdata:Record<string, DataArr[][]> = {};
        const minMaxDataCompJob:Record<string, [DataArr,DataArr][]> = {};
        const minMaxDataUser:Record<string, [DataArr,DataArr][]> = {};
        const timespan:Date[] = input.timespan??[];
        Object.keys(_jobs).forEach(k=>{
            // if (_jobs[k].total_nodes>1){
            jobs[k] = _jobs[k];
            _jobarrdata[k] = [];
            jobarrdata[k] = [];
            minMaxDataCompJob[k] = [];

            if (!jobs[k].isJobarray){
                users[_jobs[k].user_name] = _users[_jobs[k].user_name];
                userarrdata[_jobs[k].user_name] = [];
                _userarrdata[_jobs[k].user_name] = [];
                minMaxDataUser[_jobs[k].user_name] = [];
            }else{
                jobs[k].comp = {};
            }
            // }
        });

        let data = [];
        for (let comp in computers){
            interface Item_inside {key:string,values:any,value:number|undefined}
            interface Item extends Record<string,any>{
                key:string,values:(Item_inside[]| null)[],range:[number,number],data:CompOBject
            }
            let item:Item = {key:comp,values:[] ,range:[Infinity,-Infinity],data:computers[comp]};
            let job:Record<string,boolean> = {};
            computers[comp].job_id.forEach((jIDs,i)=>{
                let jobArr = jIDs.map(j=>jobs[j]).filter(d=>{
                    if (d){
                        if (!_jobarrdata[d.job_id][i]){
                            const empty:DataArrOp = [];
                            empty.name='';
                            empty.timestep=0;
                            _jobarrdata[d.job_id][i]=(empty as DataArr);
                        }
                        const tsne_comp = tsnedata[comp];
                        if(tsne_comp)
                            _jobarrdata[d.job_id][i].push(tsne_comp[i]);
                        if (!_userarrdata[d.user_name][i]){
                            const empty:DataArrOp = [];
                            empty.name='';
                            empty.timestep=0;
                            _userarrdata[d.user_name][i] =(empty as DataArr);
                        }
                        if(tsne_comp)
                            _userarrdata[d.user_name][i].push(tsne_comp[i]);

                        if(d.job_array_id){
                            if (!_jobarrdata[d.job_array_id][i]){
                                const empty:DataArrOp = [];
                                empty.name='';
                                empty.timestep=0;
                                _jobarrdata[d.job_array_id][i]=(empty as DataArr);
                            }
                            if (!jobs[d.job_array_id].comp[i])
                                jobs[d.job_array_id].comp[i] = {};
                            if (!jobs[d.job_array_id].comp[i][comp]){
                                jobs[d.job_array_id].comp[i][comp] = 1;
                                _jobarrdata[d.job_array_id][i].push(tsnedata[comp][i]);
                            }
                        }
                        return true;
                    }
                    return false
                });
                const key = jobArr.map(d=>d.job_id).toString();
                if (!job[key])
                    job[key]=true;
                else
                    jobArr=[];

                if (jobArr.length){
                    let username = d3.nest<JobOBject,number>().key(d=>d.job_id)
                        .rollup(d=>d3.sum<JobOBject>(d,(e:JobOBject)=>e.node_list_obj[comp])).entries(jobArr);
                    // username.total = d3.sum(username,e=>e.value)
                    username.sort((a,b)=>d3.ascending(a.key,b.key))
                    item.values.push(username);
                }else
                    item.values.push(null);
                item[''+timespan[i]] = item.values[i];
            });
            data.push(item);
        }
        Object.keys(_jobarrdata).forEach(k=>{
            _jobarrdata[k].forEach((d,i)=>{
                const timestep = _jobarrdata[k][i][0].timestep;
                let valueMin:DataArrOp= [];
                let valueMax:DataArrOp= [];
                let value:DataArrOp = _jobarrdata[k][i][0].map((d,si)=>{
                    const vals = _jobarrdata[k][i].map(d=>d[si]);
                    valueMin.push(d3.min(vals));
                    valueMax.push(d3.max(vals));
                    return  d3.mean(vals);
                });
                value.name = k;
                valueMin.name = k;
                valueMax.name = k;
                value.timestep = timestep;
                valueMin.timestep = timestep;
                valueMax.timestep = timestep;
                minMaxDataCompJob[k][i] = [valueMin as DataArr,valueMax as DataArr]
                jobarrdata[k][i] = (value as DataArr);
            })
        })

        Object.keys(_userarrdata).forEach(k=>{
            _userarrdata[k].forEach((d,i)=>{
                const timestep = _userarrdata[k][i][0].timestep;
                let valueMin:DataArrOp = [];
                let valueMax:DataArrOp = [];
                let value:DataArrOp = _userarrdata[k][i][0].map((d,si)=>{
                    const vals = _userarrdata[k][i].map(d=>d[si]);
                    valueMin.push(d3.min(vals));
                    valueMax.push(d3.max(vals));
                    return  d3.mean(vals);
                });
                value.name = k;
                valueMin.name = k;
                valueMax.name = k;
                value.timestep = timestep;
                valueMin.timestep = timestep;
                valueMax.timestep = timestep;
                minMaxDataUser[k][i] = [valueMin as DataArr,valueMax as DataArr]
                userarrdata[k][i] = (value as DataArr);
            })
        })

        data.sort((a,b)=>+a.range[0]-b.range[0])
        return {jobTimeline:data, jobarrdata,userarrdata,minMaxDataCompJob,minMaxDataUser};
    }
    handleDataComputeByUser(computers:Record<string, CompOBject>,jobs:Record<string,JobOBject>,timeStamp:Date[]){
        //start
        let data = [];
        interface OBJ extends Array<{key:string,type:string,value:number}>{
            total?:number
        }
        let obj:Record<string,{key:string,values:(OBJ|null)[],range:[number,number],data:JobOBject, arr:{time:Date,value:[any,any]}[]}> = {};
        let noJobMap:Record<string,any> = {};
        for (let j in jobs){
            obj[j] = {key:j,values:timeStamp.map(t=>null as OBJ|null),range:[Infinity,-Infinity],data:jobs[j],arr:[]};
            data.push(obj[j]);
        }
        for (let comp in computers){
            let jonj:Record<string, boolean> = {};
            noJobMap[comp] = [];
            computers[comp].job_id.forEach((jIDs,i)=>{
                if (jIDs.length){
                    jIDs.forEach(j=>{
                        if (!jonj[j]){
                            let item = obj[j];
                            if (!item.values[i]) {
                                item.values[i] = [] as OBJ;
                                // @ts-ignore
                                item.values[i].total = 0;
                            }
                            const compData = {key:comp,type:'compute',value:1};
                            const userData = {key:jobs[j].user_name,type:'user',value:1};
                            const current = item.values[i];
                            if(current!==null && current.length){
                                current.push(compData);
                                // item.values[i].push(userData);
                                current.total = (current.total??0) + jobs[j].node_list_obj[comp];
                                // item.arr.push({time:Layout.timespan[i],value:[compData,{key:j,type:'job',value:1},userData]});
                                item.arr.push({time:timeStamp[i],value:[{key:j,type:'job',value:1},userData]});
                                item.arr.push({time:timeStamp[i],value:[compData,{key:j,type:'job',value:1}]});
                                if(jobs[j].job_array_id){
                                    // item.arr.push({time:Layout.timespan[i],value:[compData,{key:jobs[j].job_array_id,type:'job',value:1},userData]});
                                    item.arr.push({time:timeStamp[i],value:[compData,{key:jobs[j].job_array_id,type:'job',value:1}]});
                                    item.arr.push({time:timeStamp[i],value:[{key:jobs[j].job_array_id,type:'job',value:1},userData]});
                                }
                            }
                            jonj[j]=true;
                        }
                    });
                }else{
                    noJobMap[comp][i] = 1;
                }
            });
        }

        // data.sort((a,b)=>+a.range[0]-b.range[0])
        return {data,noJobMap};
    }
    // @ts-ignore
    handleCUJ({computers,jobs},timestamp){
        const users:Record<string, UserOBject> = {};
        Object.keys(computers).forEach((k:string)=>{
            const d = computers[k];
            d.user=timestamp.map(()=>[]);
            d.jobName=timestamp.map(()=>[]);
            d.job_id=timestamp.map(()=>[]);
        });
        const jobData = d3.entries<JobOBject>(jobs);
        if(timestamp.length) {
            jobData.forEach(j => {
                j.value.start_Index = timestamp.findIndex((t: Date) => t > j.value.start_time);
                if (j.value.start_Index > 0)
                    j.value.start_Index--;
                else if (j.value.start_Index === -1)
                    j.value.start_Index = timestamp.length - 1;
                j.value.finish_Index = j.value.finish_time ? timestamp.findIndex((t: Date) => t > j.value.finish_time) : -1;
                if (j.value.finish_Index > 0)
                    j.value.finish_Index--;
                else if (j.value.finish_Index === -1)
                    j.value.finish_Index = timestamp.length - 1;
                j.value.node_list.forEach(comp => {
                    if (computers[comp]) {
                        for (let t = j.value.start_Index; t < j.value.finish_Index + 1; t++) {
                            computers[comp].job_id[t].push(j.key)
                        }
                    }
                })
            })
        }
        const user_job = d3.nest()
            .key((d:any)=>(d.value as JobOBject)['user_name']) //user
            .key((d:any)=>(d.key as string).split('.')[0]) //job array
            .object(jobData);
        Object.keys(user_job).forEach((k,i)=>{
            const  u =user_job[k];
            const job:string[] = [];
            let totalCore = 0;
            // @ts-ignore
            const node = _.uniq(_.flattenDeep(_.values(u).map(d=>d.map(d=>(job.push(d.key),totalCore+=d.value.cpu_cores,d.value.node_list)))));
            node.forEach(c=> computers[c]?computers[c].user.push(i):'');
            users[k]={node,job,totalCore}
        });
        return {users}
    }
    render(){
        const styles = getStyles();
        const {width, height} = this.props;
        const {dimensions,drawThreshold,selectedService} = this.state;

        return <ThemeContext.Consumer>{(theme) =>dimensions[0]?<div style={{width:width+' px', height:height+' px', position:'relative'}}>
            <MapViz width={width} height={height} data={this.state.scheme} serviceSelected={selectedService??0} drawThreshold={drawThreshold}
                    dimensions={this.state.dimensions}
                    highlightStream={'none'}
                    theme={theme}
                    scaleService={[]}/>
            <Drawer open={true} className={styles.legend}>
                <select
                    onChange={(event)=>{this.setState({selectedService:+event.target.value})}}>
                    {dimensions.map(d=><option key={d.text} value={d.order}>{d.text}</option>)}
                </select>
                <br/>
                {dimensions[selectedService??0]?<div style={{position:'relative',padding:5}}>
                    <StreamLegend
                        style={{marginTop:10}}
                        min={dimensions[selectedService??0].range[0]??0} max={dimensions[selectedService??0].range[1]??0} drawThreshold={drawThreshold} width={300} theme={theme}/>
                    <div style={{position:'absolute',top:0,left:2}}>
                        <span>Threshold: </span>
                        <br/>
                        <input type={'number'}
                               style={{width:70}}
                               value={dimensions[selectedService??0]?d3.format('.2f')(dimensions[selectedService??0].scale.invert(drawThreshold)):0}
                               onChange={(event)=>{this.setState({drawThreshold:dimensions[selectedService??0].scale(+event.target.value)})}}
                    /></div>
                </div>:''}
            </Drawer>
            {/*<div style={{position:'absolute'}}>*/}
            {/*    <select*/}
            {/*        onChange={(event)=>{this.setState({selectedService:+event.target.value})}}>*/}
            {/*        {dimensions.map(d=><option key={d.text} value={d.order}>{d.text}</option>)}*/}
            {/*    </select>*/}
            {/*    <br/>*/}
            {/*    {dimensions[selectedService??0]?<><span>Min: {d3.format('.2f')(dimensions[selectedService??0].range[0] as number)}</span>*/}
            {/*        <br/>*/}
            {/*        /!*<StreamLegend/>*!/*/}
            {/*        /!*<br/>*!/*/}
            {/*    <span>Max: {d3.format('.2f')(dimensions[selectedService??0].range[1] as number)}</span>*/}
            {/*        <br/>*/}
            {/*    <span>Threshold: </span>*/}
            {/*    <input type={'number'} value={dimensions[selectedService??0]?d3.format('.2f')(dimensions[selectedService??0].scale.invert(drawThreshold)):0}*/}
            {/*           onChange={(event)=>{this.setState({drawThreshold:dimensions[selectedService??0].scale(+event.target.value)})}}*/}
            {/*    /></>:''}*/}
            {/*</div>*/}
        </div>:'No data'}</ThemeContext.Consumer>
    }
}
const getStyles = stylesFactory(() => {
    return {
        wrapper: css`
      position: relative;
    `,
        legend: css`
      position: absolute;
      top: 0;
      left: 0;
      overflow-y:auto;
      height: 145px;
    `,
    }
})
