import React from 'react';
import * as d3 from 'd3';
// import {Selection} from 'd3';
import {ScaleLinear} from "d3-scale";
import Text from "./Text"
// import * as _ from "lodash"
import NodeElement from 'NodeElement';
import {css} from "@emotion/css";
import { stylesFactory} from '@grafana/ui';
import "./mapviz.css"
import {GrafanaTheme2} from "@grafana/data";

// function moveToFront (path:Selection<any, any, any, any>) {
//     return path.each(function (this:SVGElement) {
//         if(this.parentNode)
//             this.parentNode.appendChild(this);
//     });
// };
const userIcon = require("./img/u.png");
const userpicNoBorder = require("./img/uNoBorder.png");
let graphicopt = {
    svg: '#Chartcontent',
    margin: {top: 40, right: 10, bottom: 0, left: 100},
    offset: {top: 0},
    width: 1500,
    height: 700,
    scalezoom: 1,
    widthView: function () {
        return this.width * this.scalezoom
    },
    heightView: function () {
        return this.height * this.scalezoom
    },
    widthG: function () {
        return this.widthView() - this.margin.left - this.margin.right
    },
    heightG: function () {
        return this.heightView() - this.margin.top - this.margin.bottom
    },
    userPos: function () {
        return this.widthG() - 420;
    },
    jobPos: function () {
        return this.userPos() - 120;
    },
    computePos: function () {
        return this.jobPos() - 120;
    },
    display: {
        links: {
            'stroke-opacity': 0.7
        },
        stream: {
            yScale: d3.scaleLinear().range([0, 20]),
            yScaleUp: d3.scaleLinear().domain([0, 1 - 650 / 800]).range([0, 10]),
            yScaleDown: d3.scaleLinear().domain([-650 / 800, 0]).range([-5, 0]),
        }
    },
    user: {
        r: 10,
    },
    job: {
        r: 10,
        r_inside: 2,
    },
    radaropt: {
        // summary:{quantile:true},
        mini: true,
        levels: 6,
        gradient: true,
        w: 20,
        h: 20,
        showText: false,
        margin: {top: 0, right: 0, bottom: 0, left: 0},
    },
    userStreamMode: 'Power'
};
interface DataObject extends Record<string, Date | number | string | undefined | null> {
};
interface RadarDataSingle {
    axis: string,
    value: number,
    enable?: boolean,
    angle: number
}
export type TSNEDATA = Record<string, DataArr[]>;

interface RadarData extends Array<RadarDataSingle> {
    name?: string
}
export interface UserOBject extends Record<string, any> {
    _currentjobs?: any[],
    isOpen?: boolean
}

export interface SummaryArr {
    min:number|undefined|null,
    max:number|undefined|null,
    mean:number|undefined|null,
    num:number,
}

interface TableHeader extends Array<{key: string, value: string,direction?:boolean}>{
    currentsort?: string,
    direction?: boolean
}

export interface JobOBject extends Record<string, any> {
    job_id: string,
    user_name: string,
    job_name: string,
    _node_list: any[],
    node_list: any[],
    node_list_obj: Record<string, number>,
    _node_list_obj: Record<string, number>,
    summary: SummaryArr[]
}

interface Link<T, N> extends Record<string, any> {
    source: T,
    target: N
}

interface InlintData extends Record<number, number>{
    name?: string,
    timestep: number,
    value: [number|undefined,number|undefined],
    sub?:boolean,
    up?:boolean,
    text?:string
}

interface SingleStream {value:InlintData[],class:string,up:boolean};

type DrawStream = Array<SingleStream>
interface HighlightData extends DrawStream{
    displayLine?:InlintData[],
    displayText?:InlintData[],
    timesteps?: number[],
    notEmpty?:boolean
}
export interface CompOBject extends Record<string, any> {
    job_id: string[][],
    key: string,
    user: string[],
    highlightData?:HighlightData,
    drawData:HighlightData
}

interface TableData extends Array<{ key: string, value: number | undefined | null }> {
    id: string
}

// type DataByTime = Record<string, Record<string, DataObject>>;

export interface DataArr extends Array<any> {
    name: string,
    timestep: number
}

export interface schemeData {
    computers: Record<string, CompOBject>,
    emptyMap: Record<string, any[]>,
    jobArr: Record<string, DataArr[]>,
    jobs: Record<string, JobOBject>,
    minMaxData: Record<string, DataArr[]>,
    timespan: Date[],
    tsnedata: TSNEDATA,
    userArr: Record<string, DataArr[]>,
    users: Record<string, any | any[]>,
}

interface Props{
    width: number,
    height: number,
    data: schemeData,
    serviceSelected: number,
    drawThreshold:number,
    highlightStream?: 'highValue'|'suddenChange'|'none',
    scaleService: ScaleLinear<number, number>[],
    dimensions:Dimension[],
    theme: GrafanaTheme2
}
interface Tablecolumn {id: string,text?: string, type: string, x: number, y: number, width: number,format?:string}
interface States {
    // datas: Record<string,any>[],
    // dataByTime: DataByTime,
    // data: schemeData,
    dimensions: Dimension[],
    selectedService?: keyof DataObject,
    // selectedTime: Date,
    // timeStamp: Date[],
    scalezoom?: number,
    margin?: { top: number, bottom: number, left: number, right: number },
    freezing: boolean,
    fisheye_scale: { x: Function, y: Function },
    _computers: string[],
    jobsObj: Record<string, JobOBject>,
    computersObj: Record<string, CompOBject>,
    usersObj: Record<string, UserOBject>,
    linkob: Record<string, Link<any, any>>,
    tableData: Record<string, any>,
    users: UserOBject[],
    jobs: JobOBject[],
    computers: CompOBject[],
    tableHeader:TableHeader,
    linkdata: Link<any, any>[],
    tableLayout:{
        row: {
            width: number,
            height: number,//deltey,
            'graph-width': number,
        },
        column: Record<string,Tablecolumn>
    },
    highlight?:{comp:Record<string, any>,user:Record<string, any>,job:Record<string, any>,link:Record<string, any>},
    highlight2?: {comp:Record<string, boolean>,user:Record<string, boolean>,job:Record<string, boolean>,link:Record<string, boolean>}
}

interface Dimension {
    range: [number, number] | [undefined, undefined],
    scale: ScaleLinear<any, any>,
    order: number,
    key?: string,
    text: string,
    active: number[],
    tickFormat?: Function,
    enable?:boolean,
    angle?: number
}
export const yUpperScale =d3.scaleLinear().domain([0, 1 - 650 / 800]).range([0, 20]);
export const yDownerScale =d3.scaleLinear().domain([-650 / 800, 0]).range([-15, 0]);
class MapViz extends React.Component<Props, States> {
    svgRef = React.createRef<SVGSVGElement>();
    _height = 0;
    widthView() {
        return this.props.width * (this.state.scalezoom ?? 1)
    }

    heightView() {
        return this.props.height * (this.state.scalezoom ?? 1)
    }

    widthG() {
        return this.widthView() - (this.state.margin ?? {
            top: 0,
            bottom: 0,
            left: 0,
            right: 0
        }).left - (this.state.margin ?? {top: 0, bottom: 0, left: 0, right: 0}).right
    }

    heightG() {
        return this.heightView() - (this.state.margin ?? {
            top: 0,
            bottom: 0,
            left: 0,
            right: 0
        }).top - (this.state.margin ?? {top: 0, bottom: 0, left: 0, right: 0}).bottom
    }

    userPos() {
        return this.widthG() - 420;
    }

    jobPos() {
        return this.userPos() - 120;
    }

    computePos() {
        return this.jobPos() - 120;
    }
    xScale = d3.scaleLinear();
    scaleNode_y_middle = d3.scaleLinear();
    contrastColor = ()=>this.props.theme.colors.text.primary;
    area_compute_up = d3.area<{timestep:number,value:(number|undefined)[]}>()
        .curve(d3.curveCatmullRom)
        .x( (d) => {
            return this.xScale(d.timestep) as number;
        })
        .y0( (d) => {
            const val =d.value[0];
            return (val!==undefined) ?-(yUpperScale(val) as number):0;
        })
        .y1( (d) => {
            const val =d.value[1];
            return (val!==undefined) ?-(yUpperScale(val) as number):0;
        })
        .defined((d) => {
            return d && d.value[0] !== undefined
        });
    area_compute_down = d3.area<{timestep:number,value:(number|undefined)[]}>()
        .curve(d3.curveCatmullRom)
        .x( (d) => {
            return this.xScale(d.timestep) as number;
        })
        .y0( (d) => {
            const val =d.value[0];
            return (val!==undefined) ?-(yDownerScale(val) as number):0;
        })
        .y1( (d) => {
            const val =d.value[1];
            return (val!==undefined) ?-(yDownerScale(val) as number):0;
        })
        .defined((d) => {
            return d && d.value[0] !== undefined
        });

    public static defaultProps = {
        width: 100,
        height: 100
    };
    animation_time=2000
    yscale = d3.scaleLinear()
    scaleJob = d3.scaleLinear()
    svg = d3.select<SVGSVGElement|null,unknown>(null)
    g = d3.select(null)
    linkg = d3.select(null)
    nodeg = d3.select(null)
    table_headerNode = d3.select(null)
    constructor(props: Props) {
        super(props);
        this.state = {
            freezing: false,
            // data: {
            //     computers: {},
            //     emptyMap: {},
            //     jobArr: {},
            //     jobs: {},
            //     minMaxData: {},
            //     timespan: [],
            //     tsnedata: {},
            //     userArr: {},
            //     users: {},
            // },
            // datas: [],
            // dataByTime: {},
            dimensions: [],
            selectedService: '',
            scalezoom: 1,
            margin: {top: 40, right: 10, bottom: 0, left: 100},
            fisheye_scale: {x: ((d: any) => d), y: ((d: any) => d)},
            // selectedTime: new Date(),
            // timeStamp:[]
            _computers: [],
            jobsObj: {},
            computersObj: {},
            usersObj: {},
            linkob: {},
            tableData: {},
            users: [],
            jobs: [],
            computers: [],
            linkdata: [],
            tableHeader: [],
            tableLayout:{
                row: {
                    width: 500,
                    height: 20,//deltey,
                    'graph-width': 85,
                },
                column: {
                    'UserID': {id: 'UserID', type: 'text', x: 10, y: 20, width: 60},
                    'Hosts': {id: 'Hosts', text: '#Computes', type: 'num', x: 120, y: 20, width: 60},
                    'Jobs': {id: 'Jobs', text: '#Jobs', type: 'num', x: 170, y: 20, width: 52},
                }
            }
        };
    }

    componentDidMount() {
        const opt: Partial<States> = this.handleData();
        const margin = this.state.margin ?? {top: 0, bottom: 0, left: 0, right: 0};
        if (opt.dimensions&&opt.dimensions.length)
            opt.selectedService = opt.dimensions[0].key;
        this.svg = d3.select(this.svgRef.current);
        this.g = this.svg.select('.pannel')
        this.linkg = this.g.select('.linkg')
        this.nodeg = this.g.select('.nodeg')
        this.table_headerNode = this.g.select('.table.header')
        const zoomFunc = d3.zoom<SVGRectElement, unknown>().on("zoom", () => {
            this.svg.select('g.pannel').attr("transform", d3.event.transform);
        });
        this.svg.select<SVGRectElement>('rect.pantarget').call(zoomFunc).call(zoomFunc.transform, d3.zoomIdentity.translate(margin.left, margin.top));
        if(this.props.data){
            let opt: Partial<States> = this.handleData();
            if (opt.dimensions&&opt.dimensions[0])
                opt.selectedService = opt.dimensions[0].key;
            const tableResult = this.updateLayout(opt);
            this.xScale = d3.scaleLinear().domain([0, this.props.data.timespan.length - 1]).range([0, graphicopt.computePos()]);
            opt= {...opt,tableHeader:tableResult.tableHeader,tableLayout:tableResult.tableLayout};
            const {computers,computersObj,users,usersObj,jobs,tableHeader,tableData,tableLayout,linkdata,freezing,highlight,highlight2} = this.draw(opt);
            opt= {...opt,computers,computersObj,users,usersObj,jobs,tableHeader,tableData,tableLayout,linkdata,freezing,highlight,highlight2}
            this.setState(opt as NonNullable<States>);
        }
    }

    componentDidUpdate(prevProps: Readonly<Props>, prevState: Readonly<States>, snapshot?: any): void {
        if ((this.props.data !== prevProps.data)) {
            let opt: Partial<States> = this.handleData();
            if (opt.dimensions&&opt.dimensions[0])
                opt.selectedService = opt.dimensions[0].key;
            const tableResult = this.updateLayout(opt);
            this.xScale = d3.scaleLinear().domain([0, this.props.data.timespan.length - 1]).range([0, graphicopt.computePos()]);
            opt= {...opt,tableHeader:tableResult.tableHeader,tableLayout:tableResult.tableLayout};
            const {computers,computersObj,users,usersObj,jobs,tableHeader,tableData,tableLayout,linkdata,freezing,highlight,highlight2} = this.draw(opt);
            opt= {...opt,computers,computersObj,users,usersObj,jobs,tableHeader,tableData,tableLayout,linkdata,freezing,highlight,highlight2}
            this.setState(opt as NonNullable<States>);
        }else if(this.props.drawThreshold !== prevProps.drawThreshold) {
            const {computers} =  this.state;
            let opt: Partial<States> = {computers};
            const {highlight2} = this.drawComp({computers});
            opt.highlight2 = highlight2;
            this.setState(opt as NonNullable<States>);
        }else if(this.props.serviceSelected !== prevProps.serviceSelected) {
            const {tableHeader,users,computers,linkdata,tableData,jobs,usersObj,computersObj} = this.state;
            this.handleSort({tableHeader,users,computers,linkdata,tableData,jobs,usersObj,computersObj} as States);
            const {highlight2} = this.drawComp({computers});
            this.setState({tableHeader,users,computers,linkdata,tableData,jobs,usersObj,computersObj,highlight2});
        }
    }

    onClick() {
        const {freezing} = this.state;
        if (freezing) {
            // g.selectAll('.node').style('pointer-events', 'auto').classed('fade', false).classed('hide', false).classed('highlight', false);
            // linkg.selectAll('.links').classed('hide', false).classed('highlight', false);
            this.setState({freezing: !freezing});
        }
    }

    handleData() {
        const {dimensions} = this.props;
        const serviceFullList = dimensions;
        const jobsObj: Record<string, JobOBject> = {};
        const computersObj: Record<string, CompOBject> = {};
        const usersObj: Record<string, UserOBject> = {};
        const linkob: Record<string, Link<any, any>> = {};
        const tableData: Record<string, TableData> = {};
        Object.keys(this.props.data.jobs).forEach(j => {
            if (!this.props.data.jobs[j].isJobarray) {
                const user_name = this.props.data.jobs[j].user_name;
                if (!usersObj[user_name]) {
                    usersObj[user_name] = this.props.data.users[this.props.data.jobs[j].user_name];
                    usersObj[user_name].key = user_name;
                    usersObj[user_name].type = 'user';
                    usersObj[user_name]._currentjobs = [];
                    usersObj[user_name].links = {};
                    usersObj[user_name].timePoints = {};
                }
                // @ts-ignore
                usersObj[user_name]._currentjobs.push(j);

                jobsObj[j] = this.props.data.jobs[j];
                jobsObj[j].key = j;
                jobsObj[j].type = 'job';
                jobsObj[j].timePoints = {};
                this.props.data.jobArr[j].forEach((d, ti) => {
                    if (d) {
                        usersObj[user_name].timePoints[ti] = 1;
                        jobsObj[j].timePoints[ti] = 1;
                    }
                });
                linkob[j + '|' + user_name] = {source: jobsObj[j], target: usersObj[user_name]};

                if (!jobsObj[j].links)
                    jobsObj[j].links = {};
                jobsObj[j].links[user_name] = j + '|' + user_name;
                usersObj[user_name].links[j] = j + '|' + user_name;

                this.props.data.jobs[j].node_list.forEach(comp => {
                    computersObj[comp] = this.props.data.computers[comp];
                    computersObj[comp].key = comp;
                    linkob[comp + '|' + j] = {source: computersObj[comp], target: jobsObj[j]};
                    if (!computersObj[comp].links)
                        computersObj[comp].links = {};
                    computersObj[comp].links[j] = comp + '|' + j;
                    jobsObj[j].links[comp] = comp + '|' + j;
                })
            }
        })

        const _computers = d3.keys(computersObj);
        //group jobs
        const users = d3.values(usersObj);
        users.forEach(u => {
            if (u._currentjobs&&(u._currentjobs.length > 10)) {
                u.isOpen = false;
                this.handleCollapseJobs(u, {jobsObj,computersObj,usersObj,linkob});
            } else {
                u.isOpen = true;
            }
        });

        const jobs = d3.values(jobsObj);
        const computers = d3.values(computersObj);

        const linkdata = d3.values(linkob);
        users.forEach((d, i) => {
            tableData[d.key] = tableData[d.key] || [{key: 'Hosts', value: 0},
                {key: 'Jobs', value: 0}];
            tableData[d.key][0].value = d.node.length;
            tableData[d.key][1].value = d.job.length;
            d.summary.forEach((s: any, i: number) => {
                if (serviceFullList[i] !== undefined) {
                    serviceFullList[i].range[1]
                    tableData[d.key][i + 2] = {
                        key: serviceFullList[i].text,
                        value: Math.round(((serviceFullList[i].range[1] as number) - (serviceFullList[i].range[0] as number)) * s.mean + (serviceFullList[i].range[0] as number))
                    };
                }
            });
            tableData[d.key].id = d.key;
            d.order = i
        });

        jobs.forEach(d=>{
            if (d.summary && !d.radarData)
                d.radarData = this.getradarData(dimensions,d.summary.map(d => d.mean as number), d.key);
            if (d.radarData) {
                d.radarData.color = d.job_ids ? this.contrastColor() : 'gray';
            }
        });
            return {_computers, jobsObj, computersObj, usersObj, linkob, tableData, users, jobs, computers, linkdata,dimensions}
    }
    getradarData(dimensions:Dimension[],a:number[], name:string) {
        let temp: RadarData = a.map((d, i) => {
            return {
                axis: dimensions[i].text,
                value: d,
                enable: dimensions[i].enable,
                angle: dimensions[i].angle ?? 0
            };
        });
        temp.sort((a, b) => a.angle - b.angle);
        temp.name = name;
        return temp
    }
    handleCollapseJobs(u: UserOBject, option: Partial<States> | undefined = undefined) {
        const serviceFullList = this.props.dimensions;
        const usersObj = option ? (option.usersObj ?? this.state.usersObj) : this.state.usersObj;
        const computersObj = option ? (option.computersObj ?? this.state.computersObj) : this.state.computersObj;
        const jobsObj = option ? (option.jobsObj ?? this.state.jobsObj) : this.state.jobsObj;
        const linkob = option ? (option.linkob ?? this.state.linkob) : this.state.linkob;
        const {data,dimensions} = this.props;
        let user_name = u.key;
        if (u.isOpen) {
            (u._currentjobs?u._currentjobs:[]).forEach(j => {
                jobsObj[j] = this.props.data.jobs[j];
                jobsObj[j].key = j;
                jobsObj[j].type = 'job';
                if (!jobsObj[j].links)
                    jobsObj[j].links = {};
                jobsObj[j].type = 'job';
                linkob[j + '|' + user_name] = {source: jobsObj[j], target: usersObj[user_name]};
                jobsObj[j].links[user_name] = j + '|' + user_name;
                u.links[j] = j + '|' + user_name;

                data.jobs[j].node_list.forEach(comp => {
                    // if compute collapse
                    if (!computersObj[comp]) {
                        computersObj[comp] = data.computers[comp];
                        computersObj[comp].key = comp;
                        computersObj[comp].links = {};
                    } else {
                        if (!computersObj[comp].links)
                            computersObj[comp].links = {};
                    }
                    computersObj[comp].links[j] = comp + '|' + j;

                    linkob[comp + '|' + j] = {source: computersObj[comp], target: jobsObj[j]};
                    jobsObj[j].links[comp] = comp + '|' + j;

                    if (jobsObj[j].summary && !jobsObj[j].radarData)
                        jobsObj[j].radarData = this.getradarData(dimensions,jobsObj[j].summary.map(d => d.mean as number), jobsObj[j].key);
                    if (jobsObj[j].radarData) {
                        jobsObj[j].radarData.color = jobsObj[j].job_ids ? this.contrastColor() : 'gray';
                    }

                });
            });
            const jobKey = u.key + 'jobColapse';

            jobsObj[jobKey].node_list.forEach(comp => {
                delete linkob[comp + '|' + jobKey];
            });
            delete jobsObj[jobKey];
            delete linkob[jobKey + '|' + user_name];

            const compKey = u.key + ' summary';
            delete computersObj[compKey];
            delete linkob[compKey + '|' + jobKey];

        } else {
            let node_listO: Record<string, boolean> = {};
            let job_ids: Record<string, JobOBject> = {};
            let timePoints: Record<string, number> = {};
            let compute_ids: Record<string, CompOBject> = {};
            let compute_tsnedata: DataArr[][] = [];
            (u._currentjobs??[]).forEach(j => {
                delete jobsObj[j];
                delete linkob[j + '|' + user_name];
                if (data.jobs[j].isJobarray) {
                    Object.keys(data.jobs[j].job_ids)
                        .forEach(k => job_ids[k] = data.jobs[k]);
                } else
                    job_ids[j] = data.jobs[j];
                data.jobs[j].node_list.forEach((comp: string) => {
                    node_listO[comp] = true;
                    delete linkob[comp + '|' + j];

                    // handle for compute collapse
                    compute_ids[comp] = data.computers[comp];
                    delete computersObj[comp].links[j];
                    if (!Object.keys(computersObj[comp].links).length) {
                        delete computersObj[comp];
                    }
                    data.jobArr[j].forEach((d, ti) => {
                        if (d) {
                            timePoints[ti] = 1;
                            if (data.tsnedata[comp][ti]) {
                                if (!compute_tsnedata[ti]) {
                                    const newData: any = [];
                                    newData.name = '';
                                    newData.timestep = 0;
                                    compute_tsnedata[ti] = newData as DataArr;
                                }
                                compute_tsnedata[ti].push(data.tsnedata[comp][ti])
                            }
                        }
                    });

                });
            });
            let summary = serviceFullList.map((s, si) => {
                let min = Infinity;
                let max = -Infinity;
                let mean = 0;
                let num = 0;
                Object.values(job_ids).forEach(j => {
                    min = Math.min(j.summary[si].min as number, min);
                    max = Math.max(j.summary[si].min as number, max);
                    if (j.summary[si].mean) {
                        mean += ((j.summary[si].mean as number) * j.summary[si].num);
                        num += j.summary[si].num;
                    }
                });
                return {min, max, mean: mean / num, num};
            });
            const jobKey = u.key + 'jobColapse';
            jobsObj[jobKey] = {} as JobOBject;
            jobsObj[jobKey].user_name = u.key;
            jobsObj[jobKey].node_list = Object.keys(node_listO);
            jobsObj[jobKey].key = jobKey;
            jobsObj[jobKey].type = 'job';
            jobsObj[jobKey].job_ids = job_ids;
            jobsObj[jobKey].summary = summary;
            jobsObj[jobKey].isjobColapse = true;
            jobsObj[jobKey].links = {};
            jobsObj[jobKey].timePoints = timePoints;

            linkob[jobKey + '|' + user_name] = {source: jobsObj[jobKey], target: usersObj[u.key]};
            jobsObj[jobKey].links[user_name] = linkob[jobKey + '|' + user_name];

            // if compute not collapse
            // jobsObj[jobKey].node_list.forEach(comp => {
            //     linkob[comp + '|' + jobKey] = {source: computersObj[comp], target: jobsObj[jobKey]};
            // })

            const compKey = u.key + ' summary';
            let comptsnedata = compute_tsnedata.map((t, ti) => {
                const d = serviceFullList.map((s, si) => {
                    return d3.mean(t, f => f[si]);
                });
                (d as any).name = compKey;
                (d as any).timestep = ti;
                return d;
            });

            let compSummary = serviceFullList.map((s, si) => {
                let min = Infinity;
                let max = -Infinity;
                let mean = 0;
                let num = 0;
                Object.values(compute_ids).forEach(j => {
                    min = Math.min(j.summary[si].min, min);
                    max = Math.max(j.summary[si].min, max);
                    if (j.summary[si].mean) {
                        mean += (j.summary[si].mean * j.summary[si].num);
                        num += j.summary[si].num;
                    }
                });
                return {min, max, mean: mean / num, num};
            });

            computersObj[compKey] = {} as CompOBject;
            computersObj[compKey].user = [u.key];
            computersObj[compKey].key = compKey;
            computersObj[compKey].compute_ids = compute_ids;
            computersObj[compKey].summary = compSummary;
            computersObj[compKey].tsnedata = comptsnedata;
            computersObj[compKey].iscomputeCollapse = true;
            computersObj[compKey].links = {};
            linkob[compKey + '|' + jobKey] = {source: computersObj[compKey], target: jobsObj[jobKey]};
            computersObj[compKey].links[jobKey] = compKey + '|' + jobKey;
            // add link to job
            jobsObj[jobKey]._node_list = jobsObj[jobKey].node_list.slice();
            jobsObj[jobKey].node_list = [compKey];
            jobsObj[jobKey].links[compKey] = compKey + '|' + jobKey;
            if (jobsObj[jobKey].summary && !jobsObj[jobKey].radarData)
                jobsObj[jobKey].radarData = this.getradarData(dimensions,jobsObj[jobKey].summary.map(d => d.mean as number), jobsObj[jobKey].key);
            if (jobsObj[jobKey].radarData) {
                jobsObj[jobKey].radarData.color = jobsObj[jobKey].job_ids ? this.contrastColor() : 'gray';
            }
        }
        const jobs = d3.values(jobsObj);
        const computers = d3.values(computersObj);
        const linkdata = d3.values(linkob);
        return {jobs,computers,linkdata};
    }
    pathRound( opt:{width:number,height:number,ctl?:number,ctr?:number,cbl?:number,cbr?:number}) {
        opt.ctl = opt.ctl || 0;
        opt.ctr = opt.ctr || 0;
        opt.cbl = opt.cbl || 0;
        opt.cbr = opt.cbr || 0;
        let bpath = '';
        if (opt.ctl)
            bpath += `M0,-${opt.ctl} q0,-${opt.ctl} ${opt.ctl},-${opt.ctl}`;
        else
            bpath += `M0,0`;
        bpath += ` h${opt.width - opt.ctr - opt.ctl}`;
        if (opt.ctr)
            bpath += `q${opt.ctr},0 ${opt.ctr},${opt.ctr}`;
        bpath += `v${opt.height - opt.ctr - opt.cbr}`;
        if (opt.cbr)
            bpath += `q0,${opt.cbr} -${opt.cbr},${opt.cbr}`;
        bpath += `h-${opt.width - opt.cbr - opt.cbl}`;
        if (opt.cbl)
            bpath += `q-${opt.cbl},0 -${opt.cbl},-${opt.cbl}`;
        bpath += `z`;

        return bpath;
    }
    onClickHeaderCell(d:{key: string, value: string,direction?:boolean}){
        const {tableHeader,users,computers,linkdata,tableData,jobs,usersObj,computersObj} = this.state;
        if (d.key !== tableHeader.currentsort)
            tableHeader.filter(e => e.key === tableHeader.currentsort)
                .forEach(function (e) {
                    e.direction = undefined;
                });
        tableHeader.currentsort = d.key;
        tableHeader.direction = (d.direction = !d.direction);
        this.handleSort({tableHeader,users,computers,linkdata,tableData,jobs,usersObj,computersObj} as States);
        this.setState({tableHeader,users,computers,linkdata,tableData,jobs,usersObj,computersObj})
    }

    updateLayout(opt:Partial<States>={}) {
        const tableLayout = opt.tableLayout??this.state.tableLayout;
        let tableHeader = opt.tableHeader??this.state.tableHeader;
        const dimensions = opt.dimensions??this.state.dimensions;
        let currentsort = tableHeader.currentsort;
        let currentdirection = tableHeader.direction;
        const _tableHeader:TableHeader = [{key: 'UserID', value: 'UserID'}, {key: 'Hosts', value: '#Computes'}, {
            key: 'Jobs',
            value: '#Jobs'
        }];
        tableLayout.column = {
            'UserID': {id: 'UserID', type: 'text', x: 10, y: 20, width: 60},
            'Hosts': {id: 'Hosts', text: '#Computes', type: 'num', x: 60, y: 20, width: 90},
            'Jobs': {id: 'Jobs', text: '#Jobs', type: 'num', x: 165, y: 20, width: 60},
        }
        let offset = tableLayout.column['Jobs'].x + tableLayout.column['Jobs'].width;
        let padding = 15;
        // if (showtable)
        dimensions.forEach((d, i) => {
            tableLayout.column[d.text] = {
                id: d.text,
                type: 'graph',
                x: offset + (i) * tableLayout.row["graph-width"] + padding,
                y: 20,
                width: tableLayout.row["graph-width"]
            };
            tableLayout.row.width = offset + (i) * (tableLayout.row["graph-width"] + padding);
            _tableHeader.push({key: d.text, value: d.text});
        });
        tableLayout.row.width += tableLayout.row["graph-width"]/2;

        _tableHeader.currentsort = currentsort;
        _tableHeader.direction = currentdirection;

            return {tableHeader:_tableHeader,tableLayout}
    }
    handleSort({tableHeader,users,computers,linkdata,tableData,jobs,usersObj,computersObj}:NonNullable<States>) {
        // const {tableHeader,users,tableData} = this.state;
        if (tableHeader.currentsort !== undefined){
            const direction = tableHeader.direction?1:-1;
            switch (tableHeader.currentsort) {
                case 'UserID':
                    users.sort((a, b) => a.key.localeCompare(b.key) * direction);
                    break;
                default:
                    var indexf = tableHeader.findIndex(d => d.key === tableHeader.currentsort) - 1;
                    users.sort((a, b) => (tableData[b.key][indexf].value - tableData[a.key][indexf].value) * direction);
                    break;
            }
        }
        users.forEach((d, i) => {
            // delete d.jobStart_order;
            d.order = i;
            d.orderlink = i;
            d.fx = graphicopt.userPos();
            d.fy = this.yscale(d.order);
            d.x = d.fx;
            d.y = d.fy;
        });

        this.renderManual({computers, jobs, linkdata,usersObj,computersObj,users} as States)
        return {computers, jobs, linkdata,usersObj,computersObj,users}
    }
    drawComp({computers}:{computers:CompOBject[]}) {
        const {getDrawData,gettsnedata} = this;
        const self = this;
        computers.forEach(d => {
            d.drawData = getDrawData.bind(self)(gettsnedata.bind(self)(d))
        });
        const {highlight2} = this.drawEmbedding_timeline();
        return {highlight2}
    }
    handleHightlight(opt: Partial<States>={}) {
        const computersObj = opt.computersObj??this.state.computersObj;
        const computers = opt.computers??this.state.computers;
        const {serviceSelected,highlightStream} = this.props;
        let listCompHighlight:Record<string, boolean> = {};
        if (highlightStream === 'highValue') {
            // find top 10
            let alldataPoint:DataArr[] = [];
            computers.forEach(d => {
                d.highlightData = d.drawData.map(d => {
                    return {value: [], class: d.class, up: d.up}
                });
                if(d.highlightData){
                    d.highlightData.displayText = [];
                    d.highlightData.displayLine = [];
                    d.highlightData.timesteps = [];
                }
                this.gettsnedata(d).forEach((e:any) => {
                    alldataPoint.push(e)
                })
            });
            alldataPoint.sort((a, b) => b[serviceSelected] - a[serviceSelected]);
            for (let i = 0; i < 10; i++) {
                const step = alldataPoint[i].timestep;
                const currentcomputersObj = computersObj[alldataPoint[i].name];
                if (currentcomputersObj !== undefined && currentcomputersObj.highlightData !== undefined) {
                    currentcomputersObj.highlightData.notEmpty = true;
                    listCompHighlight[alldataPoint[i].name] = true;
                    currentcomputersObj.highlightData.forEach((d, j) => {
                        if (currentcomputersObj.drawData[j].value[step - 1]) {
                            d.value[step - 1] = currentcomputersObj.drawData[j].value[step - 1];
                            if (currentcomputersObj.drawData[j].value[step - 1][serviceSelected] !== undefined) {
                                if(currentcomputersObj.highlightData&&currentcomputersObj.highlightData.displayLine!==undefined){
                                    currentcomputersObj.highlightData.displayLine[step - 1] = {
                                        ...d.value[step - 1] as InlintData,
                                        up:currentcomputersObj.drawData[j].up
                                    };
                                }
                            }
                        }
                        if (currentcomputersObj.drawData[j].value[step][serviceSelected] !== undefined) {
                            d.value[step] = currentcomputersObj.drawData[j].value[step];
                            if(currentcomputersObj.highlightData){
                                if (currentcomputersObj.highlightData.displayLine) {
                                    currentcomputersObj.highlightData.displayLine[step] = {
                                        ...d.value[step] as InlintData,
                                        up: currentcomputersObj.drawData[j].up
                                    };
                                }
                                if (currentcomputersObj.highlightData.displayText){
                                    const text = ''+Math.round(this.props.scaleService[serviceSelected].invert(d.value[step][serviceSelected]));
                                    currentcomputersObj.highlightData.displayText.push({
                                        ...currentcomputersObj.drawData[j].value[step],
                                        up: currentcomputersObj.drawData[j].up,
                                        text
                                    });
                                }
                                if (currentcomputersObj.highlightData.timesteps)
                                    currentcomputersObj.highlightData.timesteps.push(step);
                            }
                        }
                        if (currentcomputersObj.drawData[j].value[step + 1]) {
                            d.value[step + 1] = currentcomputersObj.drawData[j].value[step + 1];
                            if (currentcomputersObj.drawData[j].value[step + 1][serviceSelected] !== undefined) {
                                if (currentcomputersObj.highlightData && currentcomputersObj.highlightData.displayLine) {
                                    currentcomputersObj.highlightData.displayLine[step + 1] = {
                                        ...d.value[step + 1],
                                        up: currentcomputersObj.drawData[j].up,
                                    };
                                }
                            }
                        }
                    });
                    // currentcomputersObj.highlightData.displayLine.sort((a,b)=>a.timestep-b.timestep)
                }
            }
        } else if (highlightStream === 'suddenChange') {
            // find top 10
            let alldataPoint:{name: string, timestep: number, value: number,valueRaw: number}[] = [];
            computers.forEach(d => {
                d.highlightData = d.drawData.map(d => {
                    return {value: [], class: d.class, up: d.up}
                });
                d.highlightData.displayText = [];
                d.highlightData.displayLine = [];
                d.highlightData.timesteps = [];
                const arr = this.gettsnedata(d);
                for (let i = 1; i < arr.length; i++) {
                    if ((arr[i - 1] !== undefined) && (arr[i] !== undefined)) {
                        const pre = (arr[i - 1][serviceSelected] === 0) ? 0 : (arr[i - 1][serviceSelected] - 1);
                        const current = (arr[i][serviceSelected] === 0) ? 0 : (arr[i][serviceSelected] - 1);
                        const sudden = (current + 1) / (pre + 1);
                        alldataPoint.push({
                            name: d.key,
                            timestep: i,
                            value: sudden,
                            valueRaw: arr[i][serviceSelected] - arr[i - 1][serviceSelected]
                        })
                    }
                }
            });
            alldataPoint.sort((a, b) => Math.abs(b.value) - Math.abs(a.value));
            for (let i = 0; i < 10; i++) {
                const step = alldataPoint[i].timestep;
                const currentcomputersObj = computersObj[alldataPoint[i].name];
                if(currentcomputersObj.highlightData){
                    currentcomputersObj.highlightData.notEmpty = true;
                    listCompHighlight[alldataPoint[i].name] = true;
                    currentcomputersObj.highlightData.forEach((d, j) => {

                        const up = currentcomputersObj.drawData[j].up;
                        if (currentcomputersObj.drawData[j].value[step - 1]) {
                            d.value[step - 1] = currentcomputersObj.drawData[j].value[step - 1];
                            if (currentcomputersObj.drawData[j].value[step - 1][serviceSelected] !== undefined) {
                                if(currentcomputersObj.highlightData&&currentcomputersObj.highlightData.displayLine)
                                    currentcomputersObj.highlightData.displayLine[step - 1] = {
                                        ...d.value[step - 1],
                                        up
                                    };
                            }
                        }
                        if (currentcomputersObj.drawData[j].value[step][serviceSelected] !== undefined) {
                            d.value[step] = currentcomputersObj.drawData[j].value[step];
                            if(currentcomputersObj.highlightData&&currentcomputersObj.highlightData.displayLine)
                                currentcomputersObj.highlightData.displayLine[step] = {
                                    ...d.value[step],
                                    up
                                };
                            if(currentcomputersObj.highlightData&&currentcomputersObj.highlightData.displayText)
                                currentcomputersObj.highlightData.displayText.push({
                                    ...currentcomputersObj.drawData[j].value[step],
                                    up: currentcomputersObj.drawData[j].up,
                                    text: ''+Math.round(this.props.scaleService[serviceSelected].invert(alldataPoint[i].valueRaw))
                                });
                            if(currentcomputersObj.highlightData&&currentcomputersObj.highlightData.timesteps)
                                currentcomputersObj.highlightData.timesteps.push(step);
                        }

                    });
                }
                // currentcomputersObj.highlightData.displayLine.sort((a,b)=>a.timestep-b.timestep)
            }
        } else {
            computers.forEach(d => d.highlightData = undefined);
        }
        if ((highlightStream===undefined) || (highlightStream !== 'none')) {
            let listUserHighlight: Record<string, boolean> = {};
            let listJobHighlight: Record<string, boolean> = {};
            let listLinkHighlight: Record<string, boolean> = {};
            let link = opt.linkdata??this.state.linkdata;


            computers.forEach(d => {
                if(listCompHighlight[d.key]){
                    const joblist:Record<string, boolean> = {};
                    if(d.highlightData && d.highlightData.timesteps){
                        d.highlightData.timesteps.forEach(i => d.job_id[i].forEach(j => joblist[j] = true))
                        const joblists = Object.keys(joblist);

                        const samesourceObj:Record<string, any> = {};
                        link.filter(f => {
                            if ((d.key === f.source.key)&& (f.target.job_ids ? joblists.find(j => f.target.job_ids[j])!==undefined : joblist[f.target.key])){
                                const keys:string = ''+f.target.key+f.source.key;
                                listLinkHighlight[keys] = true;
                                samesourceObj[f.target.key]=true;
                                listJobHighlight[f.target.key]=true;
                            }
                        });
                        link.filter(f => {
                            if (samesourceObj[f.source.key]){
                                listUserHighlight[f.target.key]=true;
                            }
                        });

                    }
                }
            })
            return {comp:listCompHighlight,job:listJobHighlight,user:listUserHighlight,link:listLinkHighlight}
        } else {
            return undefined;
        }
    }
    drawEmbedding_timeline(opt: Partial<States>={}) {
        const {handleHightlight} = this;
        const computersObj = opt.computersObj??this.state.computersObj;
        const computers = opt.computers??this.state.computers;
        const highlight2 = handleHightlight.bind(this)({computers,computersObj});
        return {highlight2}
    }
    getDrawData(n:DataArr[]):HighlightData {
        const {serviceSelected,drawThreshold} = this.props;
        const scheme = this.props;
        const drawData:HighlightData = [{
            value: n.map((d, ti) => {
                if (this.props.data.emptyMap[d.name] && this.props.data.emptyMap[d.name][ti] || d[serviceSelected] === undefined)
                    return {...d, value: [undefined, undefined]}
                if ((d[serviceSelected] - drawThreshold) > 0) {
                    return {...d, value: [0, d[serviceSelected] - drawThreshold]};
                }
                return {value: [0, 0], timestep : d.timestep};
            }), class: "upStream",
            up: true
        },
            {
                value: n.map((d, ti) => {
                    if (scheme.data.emptyMap[d.name] && scheme.data.emptyMap[d.name][ti] || d[serviceSelected] === undefined)
                        return {...d, value: [undefined, undefined]}
                    if ((d[serviceSelected] - drawThreshold) < 0)
                        return {...d, value: [d[serviceSelected] - drawThreshold, 0]};
                    return {value: [0, 0], timestep : d.timestep};
                }), class: "downStream",
                up: false
            }];
        if(n[0]){
            const name = n[0].name;
            if (scheme.data.emptyMap[name]) {
                drawData.push({
                        value: n.map((d, ti) => {
                            if (!scheme.data.emptyMap[name][ti] || d[serviceSelected] === undefined)
                                return {...d, value: [undefined, undefined]}
                            if ((d[serviceSelected] - drawThreshold) > 0) {
                                return {...d, value: [0, d[serviceSelected] - drawThreshold]};
                            }
                            return {value: [0, 0], timestep : d.timestep};
                        }), class: "emptyStream",
                        up: true
                    },
                    {
                        value: n.map((d, ti) => {
                            if (!scheme.data.emptyMap[name][ti] || d[serviceSelected] === undefined)
                                return {...d, value: [undefined, undefined]}
                            if ((d[serviceSelected] - drawThreshold) < 0)
                                return {...d, value: [d[serviceSelected] - drawThreshold, 0]};
                            return {value: [0, 0], timestep : d.timestep};
                        }), class: "emptyStream",
                        up: false
                    })
            }
        }
        // n.drawData = drawData;
        return drawData;
    }
    gettsnedata(d:any) {
        return d.tsnedata ?? this.props.data.tsnedata[d.key];
    }
    draw(opt: Partial<States>={}) {
        const computersObj = opt.computersObj??this.state.computersObj;
        const usersObj = opt.usersObj??this.state.usersObj;
        const jobs = opt.jobs??this.state.jobs;
        const tableHeader = opt.tableHeader??this.state.tableHeader;
        const tableData = opt.tableData??this.state.tableData;
        const tableLayout = opt.tableLayout??this.state.tableLayout;
        const users = opt.users??this.state.users;
        const computers = opt.computers??this.state.computers;
        const linkdata = opt.linkdata??this.state.linkdata;
        const {yscale,scaleJob} = this;
        yscale.domain([-1, Object.keys(users).length]).range([0, this.heightG()]);
        let deltey = (yscale(1)??0) - (yscale(0)??0);

        scaleJob.domain([0, Object.keys(users).length - 1]).range(yscale.range());

        tableLayout.row.height = Math.min(deltey, 30);

        // table
        this.handleSort({tableHeader,users,computers,linkdata,tableData,jobs,usersObj,computersObj} as States);


        const {highlight2} = this.drawComp({computers});
        return {computers,computersObj,users,usersObj,jobs,tableHeader,tableData,tableLayout,linkdata,freezing:false,highlight:undefined,highlight2}
    }

    updatelayerpath(d:SingleStream) {
        const {area_compute_up,area_compute_down} = this;
        return {"d": (d.up ? area_compute_up(d.value) : area_compute_down(d.value))??''};
    }
    updatelayerline(v:InlintData[]) {
        const {xScale}=this;
        let linecompute = d3.line<InlintData>()
            .curve(d3.curveCatmullRom)
            .x( (d) => {
                return xScale(d.timestep) as number;
            }).y( (d) => {
                if(d.up)
                    return -(yUpperScale( d.value[1] as number) as number);
                else
                    return -(yDownerScale(d.value[0] as number) as number);
            })
            .defined(function (d) {
                return d && ((d.up ? d.value[0] : d.value[1]) !== undefined)
            });
        const renderpath = linecompute(v)??'';
        return {'stroke':this.contrastColor(),'fill':'none','d':renderpath}

    }
    updatelayerText(d:InlintData) {
        const {xScale}=this;
        return {'fill': this.contrastColor(), 'textAnchor': 'middle',
            'x': xScale(d.timestep) as number,
            'y': d.up ? -(d.value[1]?yUpperScale(d.value[1]) as number:0) : -(d.value[0]?yDownerScale(d.value[0]) as number:0),
            'dy': d.up ? -3 : 3};
    }
    onFilterComputeHighlight(d:CompOBject, timePoint?:(number|string)[], isall?:boolean) {
        if (isall) {
            d.highlightRange = d.drawData.map(d => {
                return {value: d.value, class: d.class, up: d.up}
            });
            return <>
                {d.highlightRange.map((d:SingleStream)=><path className={'durationHighlight '+d.class} {...this.updatelayerpath(d)}/>)}
            </>
        } else {
            if (timePoint) {
                d.highlightRange = d.drawData.map(d => {
                    return {value: [], class: d.class, up: d.up}
                });
                timePoint.forEach((t:any) => {
                    d.highlightRange.forEach((h:SingleStream, i:number) => {
                        // if (d.drawData[i].value[t][serviceSelected] !== undefined) {
                        h.value[t] = d.drawData[i].value[t];
                        // }
                    })
                });
                d.highlightRange.forEach((h:SingleStream, i:number) => {
                    if (h.value.length) {
                        let last = h.value[h.value.length - 1];
                        if (!last.sub) {
                            if (!h.value[h.value.length - 2]) {
                                const t = last.timestep + 1;
                                if (d.drawData[i].value[t]) {
                                    const sub = {...d.drawData[i].value[t]};
                                    sub.name = d.drawData[i].value[t].name;
                                    sub.timestep = d.drawData[i].value[t].timestep - 0.5;
                                    // @ts-ignore
                                    sub.value = sub.value.map((v, j) => (d.drawData[i].value[t - 1].value[j] + v) / 2);
                                    sub.sub = true;
                                    h.value.push(sub)
                                }
                            }
                        }
                    }
                })
                return <>
                    {d.highlightRange.map((d:SingleStream)=><path className={'durationHighlight '+d.class} {...this.updatelayerpath(d)}/>)}
                </>
            } else {
                delete d.highlightRange;
                return '';
            }
        }
    }

    renderManual({computers, jobs,usersObj,computersObj, linkdata}: States) {
        let {scaleJob,yscale,scaleNode_y_middle} = this;
        const {serviceSelected,data} = this.props;
        jobs.sort((a, b) => usersObj[a.user_name].order - usersObj[b.user_name].order);
        let jobCount = 0;
        let jobNum = jobs.length;
        jobs.forEach((d, i) => {
            d.order = jobCount;
            jobCount += 0.5;
            if (i < jobNum - 2) {
                if (jobs[i + 1].user_name !== d.user_name)
                    jobCount += 1;
            }
        });
        scaleJob.domain([0, jobCount]);
        this._height = scaleJob.range()[1];
        jobs.forEach(d=>{
            d.x2 = graphicopt.jobPos();
            d.y = scaleJob(d.order);
        });


        let temp_link = linkdata.filter(d => d.target.type === 'job');

        // computers.data().forEach(d => d.y = d3.mean(temp_link.filter(e => e.source.key === d.key), f => f.target.y));
        computers.forEach(d => d.y = d3.min(temp_link.filter(e => e.source.key === d.key), f => f.target.y));
        let comps = computers.slice().sort((a, b) => a.y - b.y);
        comps.forEach((d, i) => d.order = i);

        let checkComp:Record<string, boolean> = {};
        let checkCompNum = 0;
        let computerNum = comps.length;
        let computeCount = 0;

        jobs.find(j => {
            let node_list = j.node_list.filter(comp => !checkComp[comp]);
            if (node_list.length > 2) {
                let nodeo:Record<string, {
                    el: Object,
                    mse: Record<string, {key: CompOBject, value: number|undefined}>,
                    min: {value: number|undefined, key?: CompOBject|undefined}
                }> = {};
                let min = {value: Infinity as(number|undefined), key: undefined as (CompOBject|undefined)};

                for (let i = 0; i < node_list.length; i++) {

                    nodeo[node_list[i]] = {
                        el: computersObj[node_list[i]],
                        mse: {},
                        min: {value: Infinity, key: undefined}
                    };
                    checkComp[node_list[i]] = true;
                    checkCompNum++;
                }

                for (let i = 0; i < node_list.length - 1; i++) {
                    for (let z = i + 1; z < node_list.length; z++) {
                        let mse = d3.mean(data.tsnedata[node_list[i]].map((d, ti) => Math.pow(d[serviceSelected] - data.tsnedata[node_list[z]][ti][serviceSelected], 2)))??0;
                        nodeo[node_list[i]].mse[node_list[z]] = {key: computersObj[node_list[z]], value: mse};
                        if (mse < (nodeo[node_list[i]].min.value as number)) {
                            nodeo[node_list[i]].min.value = mse;
                            nodeo[node_list[i]].min.key = computersObj[node_list[z]];
                            if (mse < (min.value as number)) {
                                min.value = mse;
                                min.key = computersObj[node_list[i]];
                            }
                        }
                        nodeo[node_list[z]].mse[node_list[i]] = {key: computersObj[node_list[i]], value: mse};
                        if (mse < (nodeo[node_list[i]].min.value as number)) {
                            nodeo[node_list[z]].min.value = mse;
                            nodeo[node_list[z]].min.key = computersObj[node_list[i]];
                            if (mse < (min.value as number)) {
                                min.value = mse;
                                min.key = computersObj[node_list[z]];
                            }
                        }
                    }
                }
                if (!min.key)
                    min.key = computersObj[node_list[0]]
                let current:CompOBject|undefined = min.key??{};
                let count = 0;
                current.order = computeCount;
                computeCount += 0.5;
                count++;
                while (count < node_list.length) {
                    // find the lowest mse
                    let min = {value: Infinity as(number|undefined), key: undefined as (CompOBject|undefined)};
                    if(current){
                        Object.values(nodeo[current.key].mse).forEach(d => {
                            if ((d.value as number) < (min.value as number)) {
                                min = {...d};
                            }
                            // @ts-ignore
                            delete nodeo[d.key.key].mse[current.key];
                        });
                        if (min.key === undefined)
                            min = {...Object.values(nodeo[current.key].mse)[0]}
                    }
                    current = min.key;
                    if(current)
                        current.order = computeCount;
                    computeCount += 0.5;
                    count++;
                }
                computeCount += 1;
            } else if (node_list.length) {
                node_list.forEach(n => {
                    computersObj[n].order = computeCount;
                    computeCount += 0.5;
                    checkComp[n] = true;
                    checkCompNum++;
                });
                computeCount += 1;
            }
            return checkCompNum === computerNum;
        });

        scaleNode_y_middle.range(yscale.range()).domain([0, computeCount]);
        computers.forEach(d=>{
            d.x2 = graphicopt.computePos();
            d.y2 = scaleNode_y_middle(d.order);
        });
    }
    onClickUser(u:UserOBject,e?:any){
        if(e)
            e.stopPropagation();
        // (d3.event as Event).stopPropagation();
        if (u._currentjobs&&(u._currentjobs.length > 1)) {
            u.isOpen = !u.isOpen;
            const {jobsObj,computersObj,usersObj,linkob} = this.state;
            const {jobs,computers,linkdata} = this.handleCollapseJobs(u,{jobsObj,computersObj,usersObj,linkob});
            let opt: Partial<States> = {jobsObj,computersObj,usersObj,linkob,computers,jobs,linkdata};
            const {users,tableHeader,tableData,tableLayout,freezing,highlight,highlight2} = this.draw(opt);
            opt= {...opt,computers,computersObj,users,usersObj,jobs,tableHeader,tableData,tableLayout,linkdata,freezing,highlight,highlight2}
            this.setState(opt as NonNullable<States>);
        }
    }
    onMouseOverCompute(d:CompOBject){
        const highlight:{comp:Record<string, any>,user:Record<string, any>,job:Record<string, any>,link:Record<string, any>} = {comp:{},user:{},job:{},link:{}};
        highlight.comp[d.key] = true;
        const link = this.state.linkdata;
        const samesourceObj:Record<string, any> = {};
        link.filter(f => {
            if (d.key === f.source.key){
                const keys:string = f.source.key+'-'+f.target.key;
                highlight.link[keys] = true;
                samesourceObj[f.target.key]=true;
                highlight.job[f.target.key]=true;
            }
        });
        link.filter(f => {
            if (samesourceObj[f.source.key]){
                highlight.user[f.target.key]=true;
                const keys:string = f.source.key+'-'+f.target.key;
                highlight.link[keys] = true;
            }
        });

        this.setState({highlight})
    }
    onMouseOverUser(d:UserOBject){
        const {computers} = this.state;
        const highlight:{comp:Record<string, any>,user:Record<string, any>,job:Record<string, any>,link:Record<string, any>} = {comp:{},user:{},job:{},link:{}};
        highlight.user[d.key] = true;
        const link = this.state.linkdata;
        const sametargetObj:Record<string, any> = {};
        link.filter(f => {
            if (d.key === f.target.key){
                const keys:string = f.source.key+'-'+f.target.key;
                highlight.link[keys] = true;
                sametargetObj[f.source.key]=true;
                highlight.job[f.source.key]=true;
                return true;
            }
            return false;
        });
        const samesource = link.filter(f => {
            if (sametargetObj[f.target.key]){
                const keys:string = f.source.key+'-'+f.target.key;
                highlight.link[keys] = true;
                // highlight.comp[f.source.key]=[];
                return true;
            }
            return false;
        });

        computers.forEach(f=>{
                const link = samesource.filter(e => e.source.key === f.key);
                if (link.length) {
                    let timePoints:Record<string, any> = {};
                    link.forEach(l => {
                        Object.keys(l.target.timePoints).forEach(t => timePoints[t] = 1);
                    });
                    highlight.comp[f.key]=Object.keys(timePoints);
                }
        });
        this.setState({highlight})
    }
    onMouseOverJob(d:JobOBject){
        const {computers} = this.state;
        const highlight:{comp:Record<string, any>,user:Record<string, any>,job:Record<string, any>,link:Record<string, any>} = {comp:{},user:{},job:{},link:{}};
        highlight.job[d.key] = true;
        const link = this.state.linkdata;
        link.filter(f => {
            if (d.key === f.source.key){
                const keys:string = f.source.key+'-'+f.target.key;
                highlight.link[keys] = true;
                highlight.user[f.target.key]=true;
            }
        });
        const sametarget = link.filter(f => {
            if (d.key === f.target.key){
                const keys:string = f.source.key+'-'+f.target.key;
                highlight.link[keys] = true;
                highlight.comp[f.source.key]=[];
                return true;
            }
            return false
        });
        computers.forEach(f=>{
            if(highlight.comp[f.key]){
                const link = sametarget.filter(e => e.source.key === f.key);
                if (link.length) {
                    let timePoints:Record<string, any> = {};
                    link.forEach(l => {
                        // debugger
                        // this.props.data.emptyMap[l.source.key]
                        Object.keys(l.target.timePoints).forEach(t => timePoints[t] = 1);
                    });
                    highlight.comp[f.key]=Object.keys(timePoints)
                }
            }
        })
        this.setState({highlight})
    }
    onRelease(){
        this.setState({highlight:undefined})
    }
    render() {
        const styles = getStyles(this.props.theme);
        const {width, height} = this.props;
        const {timespan} = this.props.data;
        const {computers,jobs,users,linkdata,tableLayout,tableHeader,tableData,highlight2,highlight,fisheye_scale} = this.state;
        const {scaleNode_y_middle,yscale,xScale} = this;
        const linkHorizontal = d3.linkHorizontal<Link<any,any>,{x:number,y:number}>()
            .x(function (d) {
                return d.x as number;
            })
            .y(function (d) {
                return d.y as number;
            });
        const delta_h = Math.max(tableLayout.row.height / 2, 15);
        const limitTime = [timespan[0],timespan[timespan.length-1]];
        let rangey = d3.extent(computers, d => d.y2 === undefined ? d.y : d.y2);
        let heightRate = Math.max(this.heightG(),yscale.domain()[1]*100)/this._height;
        let scale = d3.scaleTime().range(xScale.domain()).domain(limitTime);
        const rScale = d3.scaleLinear().range([0,graphicopt.radaropt.w/2])
        const radarLine = d3.radialLine<any>()
            // .interpolate("linear-closed")
            .curve(d3.curveCardinalClosed.tension(0.7))
            .radius((d) => {
                return rScale(d.value) as number;
            })
            .angle(function (d) {
                return d.angle as number;
            });
        // const yUpperScale = graphicopt.display.stream.yScaleUp;
        // const yDownerScale = graphicopt.display.stream.yScaleDown;

        // const scaleJob = d3.scaleLinear().domain([0, Object.keys(data.users).length - 1]).range(yscale.range());
        // tableLayout.row.height = Math.min(deltey, 30);



        return <div style={{width: width + ' px', height: height + ' px', position: 'relative'}}>
            <svg width={width}
                 height={height}
                 viewBox={`0 0 ${width} ${height}`}
                 style={{
                     position: 'absolute',
                     top: 0,
                     left: 0,
                     // backgroundColor:'white'
                 }}
                 className={styles.svg}
                 ref={this.svgRef}>
                <defs>
                    <pattern id={"userpic"} width='100%' height='100%' patternContentUnits='objectBoundingBox'>
                        <image height={1} width={1} preserveAspectRatio='none' href={userIcon.default}/>
                    </pattern>
                    <pattern id={"userpicNoBorder"} width='100%' height='100%' patternContentUnits='objectBoundingBox'>
                        <image height={1} width={1} preserveAspectRatio='none' href={userpicNoBorder.default}/>
                    </pattern>
                </defs>
                <rect className={"pantarget"} width={width} height={height} onClick={() => this.onClick()} opacity="0"/>
                <g className={`pannel ${highlight2?'onhighlight2':''}`}>
                    <text className={`job_title {jobs.length?'':'hide'}`} textAnchor="middle"
                          // x={this.jobPos()}
                          x={jobs[0]?jobs[0].x2:0}
                          dy={-20}
                          fill={this.contrastColor()}
                          style={{fontWeight: 'bold'}}>Jobs
                    </text>
                    <text className={"host_title hide"} textAnchor="end" x={this.computePos()} dy={-20}
                          style={{fontWeight: 'bold'}}>Nodes's timeline
                    </text>
                    <g className={"gNodeaxis"} transform={computers[0]?`translate(${(computers[0].x2 || computers[0].x) - graphicopt.computePos()},${rangey[0]})`:`translate(200,0)`}>
                        <g className={'gMainaxis'}>
                            {scale.ticks(10).map(d=><g className={'ticks'} transform={`translate(${xScale(scale(d) as number)},${0})`}>
                                <text dy='-0.5rem' textAnchor={'middle'} fill={this.contrastColor()}>{multiFormat(d)}</text>
                                <line y2={-5} vectorEffect="non-scaling-stroke" style={{stroke:this.contrastColor(),strokeWidth:0.2,strokeDasharray:'1'}}/>
                            </g>)}
                        </g>
                        {/*<g className={'gSubaxis'}></g>*/}
                    </g>
                    <g className={"annotation_back annotation"}>
                        <g className={"majorbar"}></g>
                        <text className={"tablemessage hide"}>LOADING TABLE DATA</text>
                    </g>
                    <g className={"linkg"}>
                        <path/>
                        <text></text>
                        {
                            linkdata.map(d=><g className={`links ${highlight?(highlight.link[d.source.key + "-" + d.target.key]?'highlight':'hide'):''}`}  fill={'none'} key={d.source.key + "-" + d.target.key}>
                                <path d={linkHorizontal({
                                    source: {
                                        x: (d.source.x2 === undefined ? d.source.y : d.source.x2) + (d.source.type === 'job' ? graphicopt.job.r : 0),
                                        y: (d.source.y2 === undefined ? d.source.y : d.source.y2)*heightRate,
                                    },
                                    target: {
                                        x: (d.target.x2 === undefined ? d.target.x : d.target.x2) - ((d.target.type === 'job' || (d.source.type === 'job')) ? graphicopt.job.r : 0),
                                        y: (d.target.y2 === undefined ? d.target.y : d.target.y2)*(d.target.type === 'job'?heightRate:1),
                                    }})??''}
                                      strokeWidth={0.3}
                                      strokeOpacity={0.7}
                                stroke={this.contrastColor()}/>
                                <text
                                    textAnchor={'middle'}
                                    transform={`translate(${[(((d.source.x2 || d.source.x) + (d.target.x2 || d.target.x)) / 2),(((d.source.y2 === undefined ? d.source.y : d.source.y2) + (d.target.y2 === undefined ? d.target.y : d.target.y2)) / 2)*heightRate]})`}>
                                    {d.links ? d.links : ''}
                                </text>
                            </g>)
                        }
                    </g>
                    <g className={"nodeg"}>
                        {
                            computers.map(d=><NodeElement
                                className={`node computeNode ${fixName2Class(fixstr(d.key))} ${(highlight2&&highlight2.comp[d.key])?'highlight2':''} ${highlight?(highlight.comp[d.key]?'highlight':'fade'):''}`} key={d.key}
                                transform={`translate(${d.x2},0)`}
                                freezing={this.state.freezing} setfreezing={(freezing)=>this.setState({freezing})}
                                mouseOver={()=>this.onMouseOverCompute(d)} mouseLeave={()=>this.onRelease()}
                            >
                                <g className={"computeSig"} transform={`translate(${-xScale.range()[1]},${fisheye_scale.y(scaleNode_y_middle(d.order))*heightRate})`}>
                                    {d.drawData.map(e=><path className={'linegg '+ e.class +((highlight&&highlight.comp[d.key])?' fade':'')} {...this.updatelayerpath(e)}/>)}
                                    {/*{(d.highlightData&&(!d.highlightData.notEmpty))?<>*/}
                                    {/*    {d.highlightData.map(d=><path className={'highightedLinegg '+ d.class} {...this.updatelayerpath(d)}/>)}*/}
                                    {/*    {(d.highlightData.displayLine?<path className={'highightedLine'} {...this.updatelayerline(d.highlightData.displayLine)}/>:'')}*/}
                                    {/*    {(d.highlightData.displayText??[]).map(d=><text className={'highightedText'} {...this.updatelayerText(d)}>{d.text}</text>)}*/}
                                    {/*</>:''}*/}
                                    {(highlight&&highlight.comp[d.key])?this.onFilterComputeHighlight(d,(typeof highlight.comp[d.key]==='boolean')?undefined:highlight.comp[d.key],(typeof highlight.comp[d.key]==='boolean')?true:undefined):''}
                                </g>
                                <text className={"computeSig_label"} textAnchor={"end"} dx={-10} dy={'0.5rem'} fill={this.contrastColor()} width={200}
                                transform={`translate(${-xScale.range()[1]},${fisheye_scale.y(scaleNode_y_middle(d.order))*heightRate})`}>{d.key}</text>
                            </NodeElement>)
                        }
                        {
                            jobs.map(d=><NodeElement className={`node jobNode ${fixName2Class(fixstr(d.key))} ${(highlight2&&highlight2.job[d.key])?'highlight2':''} ${highlight?(highlight.job[d.key]?'highlight':'hide'):''}`} key={d.key} transform={`translate(${d.x2},${d.y*heightRate})`}
                                                     freezing={this.state.freezing} setfreezing={(freezing)=>this.setState({freezing})}
                                                     mouseOver={()=>this.onMouseOverJob(d)} mouseLeave={()=>this.onRelease()}>
                                <circle className={"computeSig_b"} r={graphicopt.radaropt.w / 2} fill={'#ddd'} opacity={0.2} strokeWidth={0}/>
                                <g className={"jobradar"} fill={'none'}>
                                    {d.radarData?<path d={radarLine(d.radarData)??''}
                                                             // transform={`translate(${this.xScale(d.time)},${this.yScale(d.mean)})`}
                                                             fill={d.radarData.color}
                                                             fillOpacity={0.8}
                                        // stroke={this.state.colorLevels(d.mean)}
                                                             stroke={'black'}
                                                             strokeWidth={0.5}
                                    />:''}
                                </g>
                                {(highlight&&highlight.job[d.key])?<><text className={"lelftext label"} textAnchor={"end"} x={-graphicopt.job.r}>{`#Computes: ${d.node_list.length}`}</text>
                                <text className={"righttext label"} x={graphicopt.job.r}>{d.values ? `#Jobs: ${d.values.length}` : d.key}</text></>:''}
                            </NodeElement>)
                        }
                        {
                            users.map(d=><NodeElement className={`node userNode ${fixName2Class(fixstr(d.key))} ${(highlight2&&highlight2.user[d.key])?'highlight2':''} ${highlight?(highlight.user[d.key]?'highlight':'fade'):''}`} key={d.key} fill={'white'}
                                                      transform={`translate(${d.fx},${d.fy})`}
                                                      freezing={this.state.freezing} setfreezing={(freezing)=>this.setState({freezing})}
                                                      mouseOver={()=>this.onMouseOverUser(d)} mouseLeave={()=>this.onRelease()}>
                                <circle className={"userNodeSig"} r={graphicopt.user.r} />
                                <circle className={"userNodeImg"} r={graphicopt.user.r*0.8} fill={d.isOpen ? "url(#userpicNoBorder)" : "url(#userpic)"} onClick={(e)=>this.onClickUser(d,e)}/>
                                <text className={"userNodeSig_label"} dy={'0.35rem'} x={graphicopt.user.r + 4} style={{pointerEvents: 'all'}}>{d.key}</text>
                                {tableData[d.key]?<g className={'row'} transform={`translate(0,${-delta_h})`}>
                                    {tableData[d.key].map((d:{ key: string, value: number | undefined | null })=><g className={`cell ${tableLayout.column[d.key].type}`} key={d.key}
                                                                                                                    transform={`translate(${tableLayout.column[d.key].x + (d.key !== 'UserID' ? tableLayout.column[d.key].width / 2 : 0)},${tableLayout.column[d.key].y})`}>
                                        <text style={{textAnchor:d.key !== 'UserID' ? 'middle' : 'start'}}>{tableLayout.column[d.key].format?d3.format(tableLayout.column[d.key].format as string)(d.value as number):(d.value)}</text>
                                    </g>)}
                                </g>:''}
                            </NodeElement>)
                        }
                    </g>
                    <g className={"table header"} transform={`translate(${users[0]?users[0].x:this.userPos()},${-15})`}>
                        <g className={"back"} transform={`translate(0,${10})`}>
                            <path fill={'#ddd'} d={this.pathRound({width: tableLayout.row.width, height: 25, ctl: 10, ctr: 10})}/>
                        </g>
                        <g className={'row'} transform={`translate(0,${-tableLayout.row.height / 2})`}>
                            {
                                tableHeader.map(d=><g className={`cell ${tableLayout.column[d.key].type}`}
                                                      transform={`translate(${tableLayout.column[d.key].x + (d.key !== 'UserID' ? tableLayout.column[d.key].width / 2 : 0)},20)`}
                                                      onClick={()=>this.onClickHeaderCell(d)}
                                >
                                    <Text
                                        text={d.value}
                                        width={tableLayout.column[d.key].width}
                                        endsymbol={d.direction?'':((d.direction === undefined)?'':'')}
                                        style={{fontWeight: 'bold', textAnchor:d.key !== 'UserID' ? 'middle' : 'start', pointerEvents:'all', fill: 'black'}}/>
                                </g>)
                            }
                        </g>
                    </g>
                    <rect className={"fisheyeLayer"} width={xScale.range()[1] - xScale.range()[0]} height={(yscale.range()[1] - yscale.range()[0])*heightRate}
                          transform={`translate(${-graphicopt.computePos() + (xScale.range()[1] - xScale.range()[0])} ,${yscale.range()[0]*heightRate})`}
                          style={{display: "none", opacity: 0}}/>
                </g>
            </svg>
            {/*<Timeline domain={} step={}/>*/}
        </div>
    }
}
export default MapViz;
const getStyles = stylesFactory((theme:GrafanaTheme2) => {
    const upStreamColor = "rgba(252, 141, 89, 0.7)";
    const downStreamColor = "#4682b482";
    const emptyStreamColor = "rgba(0,0,0,0.5)";//"rgba(112,112,112,0.5)";
    const upStreamColorHighlight = "rgba(252, 141, 89, 1)";
    const downStreamColorHighlight = "#4682b4";
    const emptyStreamColorHighlight = "rgb(0,0,0)";

    return {svg:css`
            & .fade 
            {opacity: 0.2;}
            & text
            {fill: ${theme.colors.text.primary}}
            & .upStream
            {fill:${upStreamColor}}
            & .downStream
            {fill:${downStreamColor}}
            & .emptyStream
            {fill:${emptyStreamColor}}
            & .durationHighlight.upStream
            {fill:${upStreamColorHighlight}}
            & .durationHighlight.downStream
            {fill:${downStreamColorHighlight}}
            & .durationHighlight.emptyStream
            {fill:${emptyStreamColorHighlight}}
            `}
})
function fixName2Class(s:string) {
    return 'h'+s.replace(/ |#|\./gi,''); //avoid . and number format
}

function fixstr(s:string) {
    return s.replace(/ |#/gi,'');
}
const formatMillisecond = d3.timeFormat(".%L"),
    formatSecond = d3.timeFormat(":%S"),
    formatMinute = d3.timeFormat("%I:%M"),
    formatHour = d3.timeFormat("%I %p"),
    formatDay = d3.timeFormat("%a %d"),
    formatWeek = d3.timeFormat("%b %d"),
    formatMonth = d3.timeFormat("%B"),
    formatYear = d3.timeFormat("%Y");
function multiFormat(date:Date) {
    return (d3.timeSecond(date) < date ? formatMillisecond
        : d3.timeMinute(date) < date ? formatSecond
            : d3.timeHour(date) < date ? formatMinute
                : d3.timeDay(date) < date ? formatHour
                    : d3.timeMonth(date) < date ? (d3.timeWeek(date) < date ? formatDay : formatWeek)
                        : d3.timeYear(date) < date ? formatMonth
                            : formatYear)(date);
}
