import React from "react";

interface Props extends React.SVGProps<SVGTextElement>{
    text:string,
    width:number,
    endsymbol?:string
}
interface State {
    words: string[],
    elip: boolean,
    isDone: boolean,
    isDoneL: boolean,
    old_words: string,
    numWords: number,
    text:string
}
export class Text extends React.Component<Props, State>{
    textRef = React.createRef<SVGTextElement>();
    tspanRef = React.createRef<SVGTSpanElement>();
    ellipsisRef = React.createRef<SVGTSpanElement>();
    state={
        words:[],
        text:'',
        elip:true,
        old_words:'',
        numWords:0,
        isDone:false,
        isDoneL:false,
    }
    componentDidMount(): void {
        const words = this.props.text.trim().split(/\s+/);
        this.setState({words,text:words.join(' '),elip:true,old_words:'',numWords:words.length,isDone:false,isDoneL:false})
    }

    componentDidUpdate(prevProps: Readonly<Props>, prevState: Readonly<State>, snapshot?: any): void {
        if(this.props.text!== prevProps.text){
            const words = this.props.text.trim().split(/\s+/);
            this.setState({words,elip:true,old_words:'',numWords:words.length,isDone:false,isDoneL:false})
        }else if(!this.state.isDone){
            const {isDone} = this.state;
            const words = this.state.words.slice();
            if(this.tspanRef && this.tspanRef.current && !isDone){

                const opt:Partial<State>={isDone:false};

                if(this.tspanRef.current.getComputedTextLength() > (this.props.width-5) && words.length)
                {
                    opt.old_words = words.pop()??'';
                    opt.text = words.join(' ');
                    opt.words = words;
                }else{
                    if(this.state.old_words!=='')
                        opt.text = (words?(words.join(' ')+' '):'')+this.state.old_words;
                    else
                        opt.text = words.join(' ');
                    opt.isDone = true;
                }
                this.setState(opt as NonNullable<State>)
            }
        }else if(this.tspanRef.current && this.state.isDone && !this.state.isDoneL){
            const {width} = this.props;
            const {words,numWords,old_words} = this.state;
            const opt:Partial<State>={};
            if(words.length === numWords)
                opt.elip = false;
            else{
                let _old_words:string[] = [];
                if (old_words!==''){
                    _old_words = old_words.split('');
                    if (this.tspanRef.current.getComputedTextLength() > (width-5) && _old_words.length) {
                        _old_words.pop()
                        opt.old_words = _old_words.join('');
                        opt.text =  (words?(words.join(' ')+' '):'')+opt.old_words;
                    }else{
                        opt.isDoneL = true;
                    }
                }else{
                    opt.isDoneL = true;
                }
            }
            this.setState(opt as NonNullable<State>)
        }
    }

    render(){
        const {text,endsymbol,...other} = this.props;
        const _text = this.state.text;
        return <text ref={this.textRef} {...other}>
            {this.state.elip?<tspan ref={this.tspanRef}>{_text+(!this.state.elip?(endsymbol||''):'')}</tspan>:<tspan>{text+(endsymbol||'')}</tspan>}
            {this.state.elip?<tspan className={'elip'}>{'...'+(endsymbol||'')}</tspan>:''}
        </text>;
    }
}
export default Text;
